.about-us {
  padding-bottom: 105px;

  @media (max-width: $viewport--sm) {
    padding-bottom: 30px;
  }

  @media (max-width: $viewport--xs) {
    padding-bottom: 9px;
  }

  &__wrap {
    display: flex;

    @media (max-width: $viewport--sm) {
      flex-direction: column;
    }
  }

  &__wrap-img {
    width: 46.44%;
    margin-right: 40px;
    margin-bottom: 40px;

    @media (max-width: $viewport--md) {
      width: 284px;
      margin-top: 5px;
      margin-right: 30px;
      flex-shrink: 0;

      img {
        width: 100%;
        height: 280px;
        object-fit: cover;
        object-position: 47% 0;
      }
    }

    @media (max-width: $viewport--sm) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;

      img {
        width: 100%;
        height: auto;
        object-position: 0 0;
      }
    }

    @media (max-width: $viewport--xs) {
      display: none;
    }
  }

  &__wrap-text {
    width: 53.6%;
    padding-right: 121px;

    @media (max-width: $viewport--md) {
      padding-right: 0;
    }

    @media (max-width: $viewport--sm) {
      width: 100%;
    }

    .h2 {
      margin-bottom: 32px;

      @media (max-width: $viewport--xs) {
        margin-bottom: 17px;
      }
    }
  }

  &__text {
    font-family: $font-family--m;
    line-height: 22px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    p {
      margin: 0;
    }
  }

  &__text p + p {
    margin-top: 21px;
  }
}
