.agent-info {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 15.4%;
  max-width: 186px;
  padding: 22px 13px;

  font-family: $font-family--r;
  font-size: 14px;
  line-height: 15px;
  font-weight: normal;
  letter-spacing: 0.021em;
  text-align: center;

  color: $color-eclipse;
  background-color: $color-alice-blue;

  &__photo {
    width: 116px;
    height: 116px;

    border-radius: 50%;
    overflow: hidden;

    img {
      height: 100%;

      object-fit: cover;
    }
  }

  &__name {
    font-size: 18px;
    line-height: 25px;
    font-weight: bold;
    letter-spacing: 0.028em;
    margin-top: 15px;
    margin-bottom: 0;
  }

  &__post {
    margin-top: 0;
    margin-bottom: 5px;
  }

  &__text {
    margin-bottom: 19px;
  }

  &__button-container {
    display: flex;
    flex-direction: column;
  }

  &__button {
    display: block;
    margin-bottom: 10px;
    text-align: center;
    min-height: 33px;
    padding: 8px 7px;
    font-size: 14px;
    transition: all 0.3s;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      color: $color-air-force-blue;
      transition: all 0.3s;
    }
  }

  .checkbox-other {
    width: 137px;
    height: 33px;
    border-radius: 3px;
    border: 1px solid $color-pink-swan;
    padding: 9px 0 9px 25px;
    left: 0;
  }

  .checkbox-other:hover {
    border: 1px solid $color-air-force-blue;
  }

  .checkbox-other input[type="checkbox"] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    display: block;
    width: 0;
    height: 0;
  }

  .checkbox-other span {
    color: $color-cinnabar;
    font-size: 13px;
    font-weight: 500;
    font-style: normal;
  }

  .checkbox-other span::before {
    content: "";
    width: 21px;
    height: 28px;
    position: absolute;
    top: 2px;
    left: 12px;
    background: url("../img/svg/favorite-empty.svg") 50% 50% no-repeat;
    transition: all 0.3s;
  }

  .focused span::before {
    transition: all 0.3s;
  }

  .checkbox-other span:hover::before {
    transition: all 0.3s;
  }

  .checkbox-other input[type="checkbox"]:checked + span::before {
    width: 21px;
    height: 28px;
    background-image: url("../img/svg/favorite-filled.svg");
    transition: all 0.3s;
    border: none;
  }
}
