.day-offers {
  padding-top: 35px;
  padding-bottom: 46px;
  overflow: hidden;

  @include viewport--xs {
    padding-top: 22px;
    padding-bottom: 12px;
  }
}

.day-offers__title {
  margin-bottom: 46px;

  @include viewport--xs {
    margin-bottom: 20px;
  }
}

.day-offers__list {
  overflow: visible;

  @include viewport--xs {
    padding-bottom: 23px;
  }

  & > .swiper-pagination {
    bottom: 0;
    display: none;

    @include viewport--xs {
      display: block;
      bottom: -8px;
    }

    .swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0 5px;
    }

    .swiper-pagination-bullet-active {
      transform: scale(1.6);
      background-color: $color-air-force-blue;
    }
  }
}

.day-offers__wrap {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 17px;
  row-gap: 17px;
  list-style: none;
  padding-left: 0;
  margin: 0;

  @include viewport--md {
    display: flex;
    column-gap: 0;
    row-gap: 0;
  }

  .catalog__column {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0;
    justify-self: center;
  }
}
