.news {
  padding-top: 66px;
  padding-bottom: 98px;
  overflow: hidden;

  @include viewport--xs {
    padding-top: 29px;
    padding-bottom: 45px;
  }
}

.news__title {
  span {
    display: none;
  }

  @include viewport--xs {
    font-size: 0;
    line-height: 0;
    margin-bottom: 31px;

    span {
      display: block;
      font-size: 24px;
      line-height: 29px;
    }
  }
}

.news__wrap {
  overflow: visible;

  @include viewport--xs {
    padding-bottom: 35px;
  }

  .swiper-pagination {
    bottom: 0;
    display: none;

    @include viewport--xs {
      display: block;
    }
  }

  .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 5px;
  }

  .swiper-pagination-bullet-active {
    transform: scale(1.6);
    background-color: $color-air-force-blue;
  }
}

.news__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 22px;
  column-gap: 15px;
  padding-left: 0;
  margin: 0;
  list-style: none;

  @include viewport--sm {
    grid-template-columns: repeat(2, 1fr);
  }

  @include viewport--xs {
    display: flex;
    row-gap: 0;
    column-gap: 0;
  }
}

.news__item {
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 0 0 9px 0 rgba($color-default-black, 0.16);

  @include viewport--xs {
    width: 293px;
    flex-shrink: 0;
  }
  .news__header {
    font-weight: 700;
  }

  &--large {
    @media (min-width: 768px) {
      grid-column-start: 1;
      grid-column-end: span 2;
      grid-row-start: 1;
      grid-row-end: span 2;

      .news__content {
        padding: 35px 27px 27px 32px;
      }

      .news__date {
        margin-bottom: 7px;
        font-size: 22px;
        line-height: 29px;
      }

      .news__header {
        max-height: (29px * 3);
        max-width: 441px;
        font-size: 24px;
        line-height: 29px;
        -webkit-line-clamp: 3;

        @include viewport--sm {
          -webkit-line-clamp: 2;
        }
      }
    }
  }
}

.news__link {
  &:hover {
    .news__img {
      img {
        transform: scale(1.05);
      }
    }
  }
}

.news__img {
  height: 0;
  padding-bottom: 72.575%;
  position: relative;

  @include viewport--xs {
    padding-bottom: 71%;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: transform 0.3s;
    object-fit: cover;
  }
}

.news__content {
  padding: 10px 25px 26px 13px;

  @include viewport--xs {
    padding: 9px 25px 26px 9px;
  }
}

.news__date {
  display: block;
  margin-bottom: 2px;
  font-size: 13px;
  line-height: 18px;
  font-weight: 500;
  color: $color-denim;
  letter-spacing: 0.2px;
}

.news__header {
  display: -webkit-box;
  margin: 0;
  max-height: (17px * 2);
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.3px;
  text-overflow: ellipsis;
  overflow: hidden;
  // line-clamp: 2;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
