.navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  @include viewport--xb {
    flex-direction: column;
  }

  a {
    color: $color-blue-whale;
    transition: color $default-transition-settings;

    &:hover,
    &:focus {
      color: $color-air-force-blue;
    }

    &:active {
      color: $color-venice-blue;
    }
  }
}

.navigation__img {
  margin-right: 37px;
  width: 102px;

  img {
    @include viewport--xb {
      max-width: 56px;
      width: 56px;
      height: 22px;
    }
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-flow: row wrap;

  @include viewport--xb {
    flex-direction: column;
    font-size: 17px;
  }

  &--estate {
    width: 590px;

    @media (max-width: 1205px) and (min-width: 1145px) {
      width: 640px;
    }

    @media (max-width: 1144px) and (min-width: 932px) {
      width: 810px;
    }

    @include viewport--xb {
      width: 100%;
    }
  }

  &--company {
    width: 410px;
    margin-left: 50px;

    @media (max-width: 1205px) and (min-width: 1145px) {
      width: 440px;
    }

    @media (max-width: 1144px) and (min-width: 932px) {
      margin-left: 20px;
      width: 550px;
    }

    @media (max-width: 1144px) and (min-width: 932px) {
      margin-left: 20px;
      width: 550px;
    }

    @include viewport--xb {
      width: 100%;
      margin-left: 0;
    }
  }
}

.navigation__item {
  &:not(:last-child) {
    margin-right: 21px;

    @media (max-width: 1205px) and (min-width: 1065px) {
      margin-right: 15px;
    }

    @media (max-width: 1064px) and (min-width: 923px) {
      margin-right: 10px;
    }
  }

  @include viewport--xb {
    width: 100%;
    margin-right: 0;
    padding: 23px 0;
    border-bottom: 2px solid $color-whisper;
  }
}

.navigation__open-mobile {
  display: none;

  @include viewport--xb {
    display: block;
  }
}

.navigation__contact {
  padding: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navigation .navigation__phone {
  width: 196px;
  margin-bottom: 20px;
  font-size: 21px;
  color: $color-air-force-blue;
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.3;
  }
}

.navigation__more {
  position: relative;
  width: 100%;
  font-size: 17px;
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.3;
  }


  &::before {
    position: absolute;
    top: 20px;
    right: 0;
    content: "";
    width: 17px;
    height: 17px;
    background-image: url("../img/svg/acc-arrow-down.svg");
    background-repeat: no-repeat;
    background-size: 17px 17px;
  }
}

.navigation__none-mobile {
  @include viewport--xb {
    display: none;
  }
}

.navigation__none-tablet {
  @include viewport--bg {
    display: none;
  }
}

.navigation__no-js {
  display: none;
}

.navigation__list--acc-none {
  @include viewport--xb {
    display: none;
  }
}

.navigation__list--acc-show {
  @include viewport--xb {
    display: block;
  }
}

.navigation__more-top {
  display: none;
  transition: opacity $default-transition-settings;

  @include viewport--xb {
    display: block;
    position: relative;
    width: 100%;
    font-size: 17px;

    &:hover,
    &:focus {
      opacity: 0.5;
    }

    &:active {
      opacity: 0.3;
    }

    &::before {
      position: absolute;
      top: 20px;
      right: 0;
      content: "";
      width: 17px;
      height: 17px;
      background-image: url("../img/svg/acc-arrow-up.svg");
      background-repeat: no-repeat;
      background-size: 17px 17px;
    }
  }
}

.navigation__more-none {
  display: none;
}
