.social__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  list-style: none;
}

.social__list--light {
  width: 196px;
}

.social__list--dark {
  width: none;
}

.social__item {
  &:hover,
  &:focus {
    opacity: 0.5;
    transition: all 0.3s;
  }

  &:active {
    opacity: 0.3;
  }
}

.social__item--dark {
  padding-right: 11px;
  transition: all 0.3s;

  &:last-child {
    padding-right: 38px;
  }
}

.social__link--light {
  outline: none;

  svg {
    color: $color-air-force-blue;
  }
}

.social__link--dark {
  outline: none;

  svg {
    color: $color-venice-blue;
  }
}
