.main-services {
  padding-top: 50px;
  padding-bottom: 55px;

  @include viewport--xs {
    padding-top: 19px;
    padding-bottom: 29px;
  }
}

.main-services__title {
  margin-bottom: 45px;

  @include viewport--xs {
    margin-bottom: 31px;
  }
}

.main-services__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 15px;
  row-gap: 15px;
  padding-left: 0;
  margin: 0;
  list-style: none;

  @media (max-width: 1200px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include viewport--xs {
    grid-template-columns: 1fr;
    row-gap: 22px;
  }
}
