.partners {
  padding-top: 55px;
  padding-bottom: 71px;

  @include viewport--xs {
    padding-top: 29px;
    padding-bottom: 34px;
  }
}

.partners__title {
  margin-bottom: 29px;

  @include viewport--xs {
    margin-bottom: 24px;
  }
}

.partners__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 48px;
  row-gap: 48px;

  padding-left: 0;
  margin: 0;
  list-style: none;

  @include viewport--sm {
    column-gap: 17px;
    row-gap: 5px;
  }
}

.partners__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.8;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: 270px;
    max-height: 150px;
    object-fit: contain;
  }
}
