.recently-watched {
  padding-bottom: 35px;
  overflow: hidden;

  @media (max-width: $viewport--xs) {
    padding-bottom: 32px;
  }

  &__title {
    margin-bottom: 20px;

    @media (max-width: $viewport--xs) {
      margin-bottom: 16px;
    }
  }

  &__swiper {
    margin-left: -10px;
    margin-right: -10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
  }

  &__wrap {
    width: 100%;
    display: flex;
    list-style: none;
    padding-left: 0;
    margin: 0;
    margin-bottom: 48px;

    .catalog__column {
      width: 100%;
      margin-right: 0;
      margin-bottom: 0;
      justify-self: center;
    }

    @media (max-width: $viewport--xs) {
      margin-bottom: 28px;
    }
  }

  &__controls-wrap {
    display: flex;
    width: 100%;
  }

  &__controls {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    min-height: 45px;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    border: 1px solid $color-air-force-blue;
    padding-top: 5px;
    padding-bottom: 5px;
    line-height: 24px;

    @media (max-width: $viewport--xs) {
      min-height: 15px;
      line-height: 18px;
      border-radius: 0;
      border: none;
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  &__pagination.swiper-pagination {
    position: static;
    display: block;
    width: auto;

    @media (max-width: $viewport--xs) {
      .swiper-pagination-bullet {
        width: 9px;
        height: 9px;
        margin: 0 5px;
        background-color: $color-dim-gray;
        opacity: 0.5;
        transition: all 0.3s ease;

        &:hover:not(.swiper-pagination-bullet-active) {
          cursor: pointer;
          opacity: 0.2;
        }
      }

      .swiper-pagination-bullet-active {
        transform: scale(1.5);
        background-color: $color-air-force-blue;
        opacity: 1;
      }
    }
  }

  &__pagination-bullet.swiper-pagination-bullet {
    width: auto;
    margin: 0 11px;
    padding: 6px 10px;
    min-width: 33px;
    height: 35px;
    border-radius: 4px;
    font-family: $font-family--r;
    font-size: 19px;
    line-height: 23px;
    font-weight: 500;
    letter-spacing: 0.025em;
    color: $color-air-force-blue;
    opacity: 1;
    background-color: $color-default-white;
    transition: all 0.5s ease;

    &:hover:not(.swiper-pagination-bullet-active) {
      cursor: pointer;
      background-color: $color-alice-blue;
      box-shadow: 0 0 0 1px $color-air-force-blue;
    }

    &:active {
      color: $color-default-white;
      background-color: $color-air-force-blue;
    }
  }

  &__pagination-bullet.recently-watched__pagination-bullet--points {
    width: 21px;
    padding: 0;
    color: $color-air-force-blue;
    cursor: default;
  }

  &__pagination-bullet.swiper-pagination-bullet-active {
    cursor: default;
    color: $color-default-white;
    background-color: $color-air-force-blue;
  }

  &__nav {
    cursor: pointer;
    transition: all 0.3 ease;

    &:hover {
      opacity: 0.7;
    }

    svg {
      display: block;
    }

    @media (max-width: $viewport--sm) {
      outline: $color-transparent;
      -webkit-tap-highlight-color: $color-transparent;
    }

    @media (max-width: $viewport--xs) {
      display: none;
    }
  }

  &__nav--prev {
    padding-left: 10px;
    padding-right: 3px;

    svg {
      transform: rotate(180deg);
    }
  }

  &__nav--next {
    cursor: pointer;
    padding-left: 3px;
    padding-right: 10px;
  }

  .swiper-button-disabled {
    display: none;
  }
}
