.button {
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;

  min-width: 71px;
  min-height: 23px;

  font-weight: 500;
  font-size: 12px;
  font-family: $font-family--r;
  line-height: 17px;
  color: $color-venice-blue;

  background-color: $color-default-white;
  border: none;
  border-radius: 3px;
  outline: none;

  transition: background-color $default-transition-settings, color $default-transition-settings;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $color-default-white;
    background-color: $color-venice-blue;
  }

  &:active {
    color: $color-default-white;
    background-color: $color-air-force-blue;
  }

  &--md {
    padding: 11px 14px 11px;
    border-radius: 6px;

    font-size: 17px;
    line-height: 1;
    text-align: center;
    letter-spacing: 0.0353em;

    &:hover,
    &:focus {
      opacity: 0.9;
    }

    &:active {
      opacity: 0.8;
    }
  }

  &--white {
    border: 1px solid $color-venice-blue;
  }

  &--blue {
    background-color: $color-venice-blue;
    color: $color-default-white;
  }

  &--border {
    min-width: 100%;
    min-height: 44px;

    border: 2px solid $color-venice-blue;
    border-radius: 3px;
  }

  &--blue-mobile {
    @media (max-width: $viewport--xs) {
      background-color: $color-venice-blue;
      color: $color-default-white;
    }
  }

  &--outline {
    font-weight: bold;
    color: $color-eclipse;
    background-color: $color-transparent;
    border: 1px solid $color-pink-swan;

    &:hover,
    &:focus {
      color: $color-eclipse;
      background-color: $color-transparent;
      border-color: $color-air-force-blue;
    }

    &:active {
      color: $color-eclipse;
      background-color: $color-transparent;
    }
  }

  &--outline-blue {
    border: 2px solid $color-allports;
    color: $color-allports;

    &:hover,
    &:focus {
      color: $color-white;
      background-color: $color-air-force-blue;
      border: 2px solid $color-air-force-blue;
    }
  }

  &--dotter-underline {
    margin-right: auto;
    margin-bottom: 18px;
    font-weight: normal;
    color: $color-air-force-blue;
    transition: all 0.3s;

    border-bottom: 1px solid $color-air-force-blue;


    &:hover,
    &:focus {
      opacity: 0.8;
      background: none;
      color: $color-air-force-blue;
      transition: all 0.3s;
    }
  }

  &--more {
    width: 100%;
    height: 51px;

    font-size: 19px;
    line-height: 23px;
    letter-spacing: 0.029em;
    border-radius: 8px;

    &:hover,
    &:focus {
      opacity: 0.9;
    }

    &:active {
      opacity: 0.8;
    }
  }

  &--catalog {
    height: 44px;
    margin-top: 12px;
  }

  &--blue-active {
    background-color: $color-venice-blue;
    color: $color-default-white;
    transition: box-shadow $default-transition-settings, background-color $default-transition-settings, color $default-transition-settings;

    &:hover,
    &:focus {
      color: $color-air-force-blue;
      background-color: $color-default-white;
      box-shadow: 0 0 0 1px $color-air-force-blue;
    }

    &:active {
      color: $color-air-force-blue;
      background-color: $color-alice-blue;
      box-shadow: none;
      outline: none;
    }

    @media (max-width: $viewport--xs) {
      color: $color-night-rider;
      background-color: $color-default-white;

      &:hover,
      &:focus {
        color: $color-air-force-blue;
        box-shadow: 0 0 0 1px $color-air-force-blue;
      }
    }
  }

  &--object {
    font-size: 14px;
    color: $color-air-force-blue;
    border: 1px solid $color-light-grey;
    padding: 14px 22px;
    transition: all 0.3s;

    &:hover,
    &:focus {
      background-color: $color-venice-blue;
      color: $color-default-white;
      border: 1px solid $color-venice-blue;
      transition: all 0.3s;
    }
  }
}

.button-mobile-user {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: transparent;

  outline: none;
  border: none;

  transition: background-color $default-transition-settings, color $default-transition-settings;
  cursor: pointer;

  &::after {
    position: absolute;
    top: -10px;
    left: -10px;

    content: "";
    width: 40px;
    height: 40px;
    background-color: transparent;
  }

  svg {
    fill: $color-default-white;
    color: $color-default-white;
    transition: background-color $default-transition-settings, color $default-transition-settings, fill $default-transition-settings;
  }

  &:hover svg,
  &:focus svg {
    fill: $color-air-force-blue;
    color: $color-air-force-blue;
  }

  &:active svg {
    opacity: 0.8;
  }
}

.button-hamburger {
  position: relative;
  width: 20px;
  height: 20px;
  background-color: transparent;
  background-image: url("../img/svg/burger.svg");
  background-repeat: no-repeat;
  background-size: 20px 20px;

  outline: none;
  border: none;

  transition: background-color $default-transition-settings, color $default-transition-settings;
  cursor: pointer;

  &::after {
    position: absolute;
    top: -12px;
    left: -10px;
    content: "";
    width: 40px;
    height: 40px;
    background-color: transparent;
  }

  &:hover,
  &:focus {
    background-image: url("../img/svg/burger-hover.svg");
  }

  &:active {
    background-image: url("../img/svg/burger-active.svg");
  }
}

.button-hamburger-active {
  position: relative;
  width: 20px;
  height: 20px;
  background-color: transparent;
  background-image: url("../img/svg/close-burger.svg");
  background-repeat: no-repeat;
  background-size: 20px 20px;

  outline: none;
  border: none;

  transition: background-color $default-transition-settings, color $default-transition-settings;
  cursor: pointer;

  &::after {
    position: absolute;
    top: -12px;
    left: -10px;
    content: "";
    width: 40px;
    height: 40px;
    background-color: transparent;
  }

  &:hover,
  &:focus {
    background-image: url("../img/svg/close-burger-hover.svg");
  }

  &:active {
    background-image: url("../img/svg/close-burger-active.svg");
  }
}
