.history {
  padding-bottom: 156px;

  @include viewport--xb {
    padding-bottom: 70px;
  }

  @include viewport--xs {
    padding-bottom: 28px;
  }

  .h2 {
    font-size: 0;
    line-height: 0;

    @media (max-width: $viewport--xs) {
      margin-bottom: 30px;
      font-size: 24px;
      line-height: 32px;
    }
  }
}
