.filter-checkbox {
  // margin-right: 95px;
  margin-right: auto;

  input[type="checkbox"] {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    display: block;
    padding: 11px;
    padding-left: 31px;
    position: relative;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 13px;
      left: 0;
      width: 19px;
      height: 18px;
      border-radius: 5px;
      border: 1px solid $color-pink-swan;
      background-repeat: no-repeat;
      background-position: top 50% left 50%;
    }
  }

  input:checked + label {
    &::before {
      background-image: url("../img/svg/check.svg");
    }
  }
}

.filter-checkboxes {
  input[type="checkbox"] {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    display: block;
    padding-left: 31px;
    margin-bottom: 3px;
    position: relative;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 4px;
      left: 0;
      width: 19px;
      height: 18px;
      border-radius: 5px;
      border: 1px solid $color-pink-swan;
      background-repeat: no-repeat;
      background-position: top 50% left 50%;
    }
  }

  input:checked + label {
    &::before {
      background-image: url("../img/svg/check.svg");
    }
  }
}

