.filter-range-row {
  display: flex;
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

  label {
    margin-right: 8px;
  }

  input {
    width: 100%;
    min-width: 0;
    background-color: $color-default-white;
    border-radius: 5px;
    border: 1px solid $color-pink-swan;
    padding: 2px 15px 3px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 20px;

    &::placeholder {
      color: $color-pink-swan;
    }
  }
}

.filter-multirange {
  label {
    display: block;
    margin-bottom: 7px;
  }
}

.filter-multirange__inputs {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 14px;
  border-radius: 5px;
  border: 1px solid $color-pink-swan;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    bottom: 2px;
    left: 50%;
    transform: translateX(-1px);
    width: 2px;
    background-color: $color-alice-blue;
    z-index: 2;
  }

  input {
    width: 45%;
    flex-grow: 1;
    border: none;
    padding: 2px 9px 3px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 20px;
  }
}

.filter-text-input {
  min-width: 206px;

  input {
    width: 100%;
    flex-grow: 1;
    border: 1px solid $color-pink-swan;
    border-radius: 5px;
    padding: 2px 7px 3px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 20px;
  }
}

.filter-input {
  flex-grow: 1;
  width: 339px;
  margin-right: 13px;
  margin-bottom: 13px;

  @include viewport--xs {
    width: 100%;
  }

  &--id {
    width: 145px;
    margin-right: 16px;

    @include viewport--xs {
      width: calc(50% - 13px);
      margin-right: 13px;
    }
  }

  input {
    width: 100%;
    flex-grow: 1;
    border: 1px solid $color-pink-swan;
    border-radius: 5px;
    padding: 12px 17px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 21px;
  }
}
