.title-object {
  margin-top: -10px;
  margin-bottom: 16px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  font-family: $font-family--r;
  color: $color-night-rider;
  letter-spacing: 0.25px;

  @media (max-width: $viewport--xs) {
    margin-top: -16px;
    margin-bottom: 14px;

    flex-direction: column;
  }
}

.title-object__wrapper {
  width: 89%;
  padding-right: 20px;

  @media (max-width: 1100px) {
    width: 100%;
  }
}

.title-object__box {
  display: flex;
  flex-direction: row;
}

.title-object__name {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;

  font-size: 30px;
  line-height: 39px;
  font-weight: 700;
  font-family: $font-family--m;
  letter-spacing: 0.65px;
  color: $color-night-rider;

  @media (max-width: 908px) and (min-width: 480px) {
    font-size: 26px;
    line-height: 29px;
  }

  @media (max-width: $viewport--xs) {
    margin-bottom: 13px;
    width: 266px;

    font-size: 24px;
    line-height: 31px;
  }
}

.title-object__address {
  margin: 0;
  margin-right: 82px;
  padding: 0;
  max-width: 580px;

  font-size: 17px;
  line-height: 26px;
  color: $color-pink-swan;

  @media (max-width: $viewport--xs) {
    display: none;
  }
}

.title-object__link {
  display: flex;
  align-items: center;

  font-size: 17px;
  line-height: 26px;
  color: $color-air-force-blue;

  outline: none;
  transition: opacity $default-transition-settings;

  @media (max-width: 789px) {
    display: none;
  }

  svg {
    margin-right: 15px;
    fill: $color-air-force-blue;
    width: 31px;
    height: 24px;
  }

  span {
    display: block;
    white-space: nowrap;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }
}

.title-object__id-box {
  width: 115px;
  margin-top: -30px;
  margin-right: 14px;

  @media (max-width: 1100px) and (min-width: 480px) {
    display: none;
  }

  @media (max-width: $viewport--xs) {
    margin: 0;
    padding-left: 5px;
  }
}

.title-object__id-name {
  margin: 0;
  margin-bottom: 16px;
  padding: 0;
  white-space: nowrap;

  font-size: 15px;
  line-height: 19px;
  color: $color-pink-swan;

  @media (max-width: $viewport--xs) {
    display: none;
  }
}

.title-object__number {
  margin: 0;
  padding: 0;

  font-size: 18px;
  line-height: 39px;
  font-weight: 500;
  color: $color-night-rider;

  @media (max-width: $viewport--xs) {
    font-size: 19px;
    line-height: 37px;
    color: $color-pink-swan;
  }
}
