.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 286px;
  margin-top: 0;
  margin-bottom: 33px;
  padding-left: 0;
  list-style: none;

  a {
    opacity: 1;
    transition: opacity $default-transition-settings;

    &:hover {
      opacity: .7;
    }
  }

  @include viewport--xs {
    justify-content: flex-start;
  }
}


.socials__item {
  margin-right: 15px;

  @include viewport--xs {
    margin-right: 17px;
  }
}

.socials__link {
  display: block;
  line-height: 0;
}
