.sorting {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding-left: 0;
  list-style: none;
}

.sorting__item {
  position: relative;
  display: flex;
  margin-right: 26px;

  color: $color-air-force-blue;

  &:last-child {
    margin-right: 26px;
  }

  &:hover,
  &:focus,
  &:active {
    color: $color-gray-3;
    transition: $default-transition-settings;
  }

  svg {
    display: block;
    margin-right: 5px;
  }
}

.sorting__button {
  padding: 0;
  font-size: 18px;
  line-height: 25px;

  color: $color-air-force-blue;
  background: none;

  &:hover,
  &:focus,
  &:active {
    background: none;
    color: $color-gray-3;
    transition: $default-transition-settings;
  }

  span {
    display: block;
    border-bottom: 2px dotted $color-air-force-blue;
  }
}
