.manager {
  position: relative;

  padding: 30px 35px;
  min-width: 270px;
  min-height: 369px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  border-radius: 3px;
  box-shadow: 1px 1px 10px 4px rgba(38, 21, 39, 0.2);
  transition: box-shadow $default-transition-settings;

  &:hover {
    box-shadow: 1px 1px 12px 8px rgba(38, 21, 39, 0.2);
  }

  @media (max-width: $viewport--lg - 1) {
    display: none;
  }

  button {
    margin-bottom: 10px;
  }
}

.manager__photo {
  height: 159px;
  width: 159px;
  border-radius: 50%;
  margin-bottom: 12px;
  object-fit: cover;
}

.manager__name {
  margin: 0;
  padding: 0;
  margin-bottom: 12px;

  font-weight: 700;
  font-size: 21px;
  color: $color-night-rider;
  font-family: $font-family--m;
}

.manager__specialty {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  width: 120px;

  font-weight: 500;
  color: $color-night-rider;
  font-family: $font-family--m;
}

.js-show-phone {
  display: block;
}

.js-hide-phone {
  display: none;
}
