.filter-dropdown {
  flex-grow: 1;
  position: relative;
  width: 186px;
  margin-right: 14px;
  margin-bottom: 14px;

  &--district {
    width: 143px;
  }

  &--street {
    width: 139px;

    @include viewport--md {
      width: 233px;
    }
  }

  &--complex {
    width: 233px;
  }

  &--floor {
    width: 131px;
  }

  &--bathroom {
    width: 152px;
  }

  // @media (max-width: 823px) {
  //   width: 50%;
  // }

  @media (max-width: 823px) {
    width: calc(50% - 13px);
    margin-right: 13px;
    margin-bottom: 13px;

    &--complex {
      width: 100%;
    }
  }
}

.filter-dropdown__btn {
  position: relative;
  width: 100%;
  padding: 12px 30px 12px 19px;
  border: 1px solid $color-pink-swan;
  background-color: $color-default-white;
  border-radius: 6px;
  color: $color-blue-whale;
  text-align: left;
  letter-spacing: 0.9px;
  cursor: pointer;
  z-index: 2;

  @include viewport--xs {
    padding: 12px 30px 13px 15px;
  }

  svg {
    position: absolute;
    top: 50%;
    right: 18px;
    transform: translateY(-50%) rotate(180deg);
    fill: $color-gray-3;

    @include viewport--xs {
      right: 8px;
    }
  }

  &:focus {
    outline: none;
  }
}

.filter-dropdown__content {
  display: none;
  position: absolute;
  top: calc(100% + 14px);
  min-width: 100%;
  padding: 6px 10px;
  border: 1px solid $color-pink-swan;
  background-color: $color-default-white;
  border-radius: 6px;
  z-index: 3;

  @include viewport--xs {
    top: calc(100% + 13px);
  }
}

.filter-dropdown__overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
}

.filter-dropdown--open {
  .filter-dropdown__btn {
    svg {
      transform: translateY(-50%);
    }
  }

  .filter-dropdown__content {
    display: block;
  }

  .filter-dropdown__overlay {
    display: block;
  }
}
