@mixin history-timeline-milestone-bubble {
  height: 12px;
  width: 12px;

  background-color: $color-white;

  //border: 3px solid #4d80a0;
  border: 3px solid $color-air-force-blue;
  border-radius: 50%;
}

.history-timeline-milestone {
  position: relative;
  z-index: 1;

  &::after {
    position: absolute;
    top: 50%;
    left: 50%;

    @include history-timeline-milestone-bubble();

    transform: translateX(-50%) translateY(-50%);

    content: "";
  }

  &__text-wrapper {
    position: absolute;
    z-index: -1;
    left: -2px;
  }

  &__text {
    position: relative;

    display: block;

    padding-left: 16px;

    @include history-timeline-mimic-border(vert);

    color: $color-gray-3;
    font-family: $font-family--r;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;

    &::after {
      position: absolute;
      left: 0;

      @include history-timeline-milestone-bubble();

      transform: translateX(-8px);

      content: "";
    }
  }

  &--zero-side {
    .history-timeline-milestone__text {
      padding-top: 37px;
      padding-bottom: 6px;

      &::after {
        left: 3.5px;
        bottom: -3px;
      }
    }

    .history-timeline-milestone__text-wrapper {
      top: 0;
    }
  }

  &--one-side {
    .history-timeline-milestone__text {
      padding-top: 6px;
      padding-bottom: 37px;

      &::after {
        left: 4px;
        top: -3.5px;
      }
    }

    .history-timeline-milestone__text-wrapper {
      bottom: 0;
    }
  }
}

@include viewport--xb {
  .history-timeline-milestone {
    width: 50%;

    &__text-wrapper {
      position: relative;
    }

    &__text {
      @include history-timeline-mimic-border(hor);

      border-left: unset;

      padding-top: 16px;
    }

    &--zero-side {
      transform: translateX(-50%);

      &::after {
        top: 3.5px;
        right: 0;
        left: unset;

        transform: translateX(6px) translateY(-8px);
      }

      .history-timeline-milestone__text {
        padding-top: 20px;
        padding-right: 15px;
        padding-bottom: 0;
        padding-left: 15px;

        &::after {
          top: 3.5px;
          left: -3.5px;

          transform: translateY(-8px);
        }
      }
    }

    &--one-side {
      transform: translateX(50%);

      &::after {
        top: 3.5px;
        left: 0;

        transform: translateX(-6px) translateY(-8px);
      }

      .history-timeline-milestone__text {
        padding-top: 20px;
        padding-right: 5px;
        padding-bottom: 0;
        padding-left: 25px;

        &::after {
          top: 3px;
          left: unset;
          right: 0;

          transform: translateX(8px) translateY(-7px);
        }
      }
    }
  }
}
