.meta__wrapper {
  padding-top: 69px;
  margin-bottom: 78px;

  display: flex;
  flex-direction: column;

  text-align: center;
  box-shadow: 1px 1px 10px 4px rgba(38, 21, 39, 0.2);
  border-radius: 5px;

  @media (max-width: 1100px) and (min-width: $viewport--sm) {
    padding: 40px;
  }

  @include viewport--xs {
    padding: 0;
    margin-bottom: 17px;
    text-align: left;

    width: 100%;

    box-shadow: none;
  }
}

.meta__title {
  margin: 0;
  padding: 0;

  font-size: 30px;
  line-height: 32px;
  font-weight: 500;
  font-family: $font-family--m;
  color: $color-blue-whale;

  @media (min-width: 1100px) {
    margin: 0 auto;
    margin-bottom: 24px;
    padding: 0;
  }

  @media (max-width: 1100px) and (min-width: $viewport--sm) {
    margin-bottom: 29px;
    font-size: 21px;
    line-height: 26px;
  }

  @include viewport--xs {
    margin-bottom: 29px;

    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    color: $color-night-rider-2;
  }
}

.meta__box {
  position: relative;
  display: flex;

  text-align: left;

  @media (max-width: 1365px) and (min-width: $viewport--sm) {
    margin: 0 auto;
  }

  @include viewport--xs {
    flex-direction: column;
  }

  .meta__img {
    position: absolute;
    bottom: -1px;
    left: 150px;
    z-index: -1;

    width: 548px;
    height: 401px;

    object-fit: cover;

    @include viewport--md {
      display: none;
    }
  }
}

.meta__form-box {
  margin-top: 27px;
  margin-left: auto;
  margin-right: 200px;

  @include viewport--bg {
    margin: 0 auto;
  }

  @include viewport--xs {
    margin: 0;
    margin-right: auto;
  }
}

.meta__data {
  max-width: 285px;
  margin-bottom: 20px;

  display: flex;
  flex-direction: column;

  @include viewport--xs {
    max-width: 100%;
  }

  input:not(:last-child) {
    margin-bottom: 20px;

    @include viewport--xs {
      margin-bottom: 11px;
    }
  }

  input {
    padding: 12px;

    outline: none;
    border-radius: 6px;
    border: 1px solid $color-light-grey;

    font-family: $font-family--r;
    font-size: 14px;
    line-height: 16px;
    color: $color-blue-whale;

    transition: opacity $default-transition-settings;

    &:hover,
    &:focus {
      opacity: 0.5;
    }

    &:active {
      opacity: 0.3;
    }

    @include viewport--xs {
      max-width: 314px;
    }
  }
}

.meta__button {
  margin-bottom: 20px;
  min-width: 285px;
  min-height: 39px;

  font-size: 14px;

  @include viewport--xs {
    max-width: 314px;
    width: 100%;
    min-height: 46px;

    font-size: 17px;
    line-height: 25px;
  }
}

.meta__text {
  max-width: 285px;

  margin-bottom: 20px;
  padding: 0;

  font-size: 13px;
  font-family: $font-family--r;
  line-height: 16px;

  a {
    color: $color-default-black;
    font-weight: 700;

    @include viewport--xs {
      font-weight: 400;
    }
  }

  @include viewport--xs {
    max-width: 314px;
  }
}
