.services-item {
  display: flex;
  flex-direction: column;
  min-height: 230px;
  // padding: 13px 29px 26px 18px;
  padding: 20px;
  box-shadow: 0 0 6px 0 rgba($color-default-black, 0.16);
  border-radius: 6px;
  overflow: hidden;
  position: relative;

  @include viewport--xs {
    padding: 22px 29px 18px 27px;
    min-height: 212px;
  }
}

.services-item__wrap {
  position: relative;
  padding-right: 76px;
  min-height: 36px;
  margin-bottom: 16px;
}

.services-item__title {
  margin: 0;
  font-size: 19px;
  line-height: 18px;
  letter-spacing: 0.6px;
}

.services-item__icon {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.services-item__text {
  margin: 0;
  margin-bottom: 38px;
  font-family: $font-family--m;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.3px;

  @include viewport--xs {
    margin-bottom: 19px;
  }
}

.services-item__btn {
  margin-top: auto;
  // padding: 7px 14px 11px;
  // font-size: 17px;
  // line-height: 23px;
  // text-align: center;
  // letter-spacing: 0.6px;
}
