.details {
  margin-bottom: 92px;

  font-size: 16px;
  line-height: 20px;
  font-family: $font-family--m;
  color: $color-night-rider-2;
  letter-spacing: 0.25px;

  @include viewport--xs {
    margin-bottom: 37px;
  }
}

.details__wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (min-width: 1100px) {
    padding-right: 45px;
  }

  @include viewport--xs {
    flex-direction: column;
  }
}

.details__title {
  display: block;
  position: relative;
  margin: 0;
  margin-bottom: 24px;
  padding: 0;

  font-size: 18px;
  font-weight: 700;
  color: $color-night-rider-2;

  cursor: not-allowed;

  &:hover,
  &:focus,
  &:active,
  &:disabled {
    pointer-events: none;
    color: $color-air-force-blue;
    background-color: transparent;
    opacity: 1;
  }

  svg {
    margin-right: 7px;

    @include viewport--xs {
      display: none;
    }
  }

  @include viewport--xs {
    margin-top: 15px;
    margin-bottom: 3px;
    display: block;
    transition: background-color $default-transition-settings, color $default-transition-settings, opacity $default-transition-settings;
    cursor: pointer;

    &:hover,
    &:focus {
      pointer-events: auto;
      opacity: 0.5;
      color: $color-air-force-blue;
      background-color: transparent;
    }

    &:active {
      pointer-events: auto;
      opacity: 0.3;
      color: $color-air-force-blue;
      background-color: transparent;
    }

    span {
      color: $color-air-force-blue;
      border-bottom: 1px dotted $color-air-force-blue;
    }
  }
}

.details__list {
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;
  align-items: center;

  font-size: 15px;
  font-weight: 400;
  color: $color-gray-2;
}

.details__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 300px;

  @media (max-width: 1200px) {
    width: 250px;
  }

  @media (max-width: 969px) {
    width: 205px;
  }

  &:not(:last-child) {
    margin-bottom: 11px;
  }
}

.details__title-dotted {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;

  &::after {
    position: absolute;
    width: 100%;
    height: 100%;
    content: " .....................................................";
  }
}

.details__value {
  display: block;
  width: 100px;

  color: $color-night-rider-2;
}
