.promo {
  position: relative;
  padding-top: 168px;
  padding-bottom: 181px;
  background-color: $color-venice-blue;

  @include viewport--xs {
    padding-top: 147px;
    padding-bottom: 14px;
    background-color: $color-default-white;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 200px;
    background: linear-gradient(0deg, $color-default-white 0%, rgba($color-default-white, 0.9) 40%, rgba($color-default-white, 0) 100%);
    z-index: 1;

    @include viewport--xs {
      height: calc(100% - 147px);
      top: 187px;
      background: linear-gradient(0deg, $color-default-white 0%, $color-default-white calc(100% - 30px), rgba($color-default-white, 0) 100%);
    }
  }

  .container {
    position: relative;
    z-index: 2;
  }
}

.promo__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;

  @include viewport--xs {
    object-fit: cover;
    max-height: 240px;
  }
}

.promo__title {
  margin-top: 0;
  margin-bottom: 11px;
  font-size: 53px;
  line-height: 1.25;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  text-align: center;
  color: $color-default-white;
  font-weight: 800;

  @include viewport--xs {
    margin-bottom: 12px;
    color: $color-air-force-blue;
    font-size: 23px;
    line-height: 30px;
    letter-spacing: 0.6px;
  }
}

.promo__subtitle {
  margin-top: 0;
  margin-bottom: 63px;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
  color: $color-default-white;
  font-weight: 500;

  @include viewport--xs {
    color: $color-night-rider;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    margin-bottom: 17px;
    letter-spacing: 0.4px;
  }
}

.promo__main {
  padding: 26px 33px 25px;
  background-color: $color-default-white;
  border-radius: 6px;

  @include viewport--xs {
    padding: 0 3px;
  }
}

.promo__content {
  display: none;

  &--show {
    display: flex;
  }
}

.promo__tabs {
  position: relative;
  margin-bottom: 22px;

  @include viewport--xs {
    margin-bottom: 16px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $color-pattens;
    bottom: 0;
    left: 0;
  }
}

.promo__tab {
  position: relative;
  padding: 0 10px 11px;
  margin-right: 17px;
  // margin-bottom: -2px;
  background-color: transparent;
  border: none;
  color: $color-blue-whale;
  font-family: $font-family--m;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.6px;
  cursor: pointer;

  @include viewport--xs {
    padding: 0;
    padding-bottom: 6px;
    font-size: 16px;
    line-height: 21px;
    letter-spacing: 0.4px;
    font-family: $font-family--r;
  }

  &::before {
    content: "";
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: $color-air-force-blue;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &:not(.promo__tab--active) {
    &:hover {
      &::before {
        opacity: 0.5;
      }
    }
  }

  &--active {
    font-weight: bold;

    &::before {
      opacity: 1;
    }
  }

  &:focus {
    outline: none;
  }

  &:last-child {
    margin-right: 0;
  }
}

.promo__nav-btn {
  position: absolute;
  top: 0;
  left: -3px;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 30px;
  height: 30px;
  background: linear-gradient(90deg, $color-default-white, $color-default-white 50%, rgba($color-default-white, 0));
  z-index: 1;
  cursor: pointer;
  transition: opacity 0.3s;

  &[aria-disabled="true"] {
    // display: none;
    opacity: 0;
  }

  svg {
    margin-bottom: 6px;
    transform: rotate(-90deg);
    fill: $color-blue-whale;
  }

  &--next {
    left: auto;
    right: -3px;
    justify-content: flex-end;
    background: linear-gradient(-90deg, $color-default-white, $color-default-white 50%, rgba($color-default-white, 0));

    svg {
      transform: rotate(90deg);
    }
  }
}
