.map {
  height: 100%;
  position: relative;
  padding: 74px 0;
}

.map__iframe {
  position: absolute;
  top: 0;
  height: 100%;
  left: 0;
  width: 100%;
  background-image: url("../img/content/map.jpg");
  background-position: center;
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .map__iframe {
      background-image: url("../img/content/map@2x.jpg");
    }
  }
}

.map__contacts {
  position: relative;
  max-width: 234px;
  top: 0;
  padding: 36px 31px 36px 26px;
  z-index: 1;
  box-shadow: 0 0 9px $color-box-shadow;
  border-radius: 6px;
  background-color: $color-white;
}

.map__contacts ul {
  margin: 0;
  padding: 0;
  margin-bottom: 6px;
}

.map__contacts-call {
  font-size: 14px;
}

.map__contacts-title {
  color: $color-charcoal;
  font-family: $font-family--m;
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 0.28px;
  line-height: 19px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 6px;
}

.map__item {
  display: flex;
  list-style: none;
  color: $color-charcoal;
  font-family: $font-family--m;
  font-size: 11px;
  font-weight: 400;
  letter-spacing: 0.28px;
  line-height: 19px;
  padding-bottom: 12px;
}

.map__item-tel:hover,
.map__item-tel:focus {
  color: $color-air-force-blue;
  transition: all 0.3s;
}

.map__item-tel {
  display: block;
  color: $color-charcoal;
  cursor: pointer;
  transition: all 0.3s;
}

.map__item svg {
  width: 12px;
  height: 14px;
  margin-right: 8px;
}

.map__card {
  width: 100%;
  height: 100%;
}

@media (max-width: $viewport--md) {
  .map__contacts {
    bottom: auto;
  }
}

@media (max-width: $viewport--sm) {
  .map {
    padding: 0;
    background-color: $color-whisper;
  }

  .map__contacts {
    max-width: 100%;
    position: inherit;
    bottom: 0;
    transform: inherit;
    padding: 50px 49px 50px 39px;
  }

  .map__contacts ul {
    padding: 0;
    margin: 0 0 34px;
    padding-top: 30px;
  }

  .map__item svg {
    width: 14px;
    height: 19px;
    margin-right: 8px;
  }

  .map__item {
    padding-bottom: 16px;
    font-size: 14px;
  }

  .map__contacts-title {
    font-size: 14px;
  }

  .map__iframe {
    display: none;
  }

  .map__container {
    padding: 25px 31px 28px 26px;
    transform: translateY(12%);
    z-index: 1;
    display: inline-block;
  }
}
