.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  letter-spacing: 0.025em;
  box-shadow: 1px 1px 10px 4px rgba(38, 21, 39, 0.2);
  transition: box-shadow $default-transition-settings;

  &:hover {
    box-shadow: 1px 1px 12px 6px rgba(38, 21, 39, 0.2);
  }

  @include viewport--xb {
    box-shadow: none;
  }
}

.header__wrapper {
  font-family: "Roboto", sans-serif;

  &--top {
    padding: 8px 0;
    font-size: 12px;
    font-weight: 500;
    color: $color-default-white;
    background-color: $color-venice-blue;

    @include viewport--xb {
      padding: 9px 0;
      padding-bottom: 8px;
    }
  }

  &--bottom {
    padding: 14px 0;
    padding-bottom: 13px;
    font-size: 14px;
    font-weight: 700;
    color: $color-blue-whale;
    background-color: $color-default-white;

    @media (max-width: 1205px) and (min-width: 932px) {
      font-size: 12px;
    }
  }
}

.header__line {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.header__menu {
  @include viewport--xb {
    display: none;
  }
}

.header__menu.header__menu-active {
  @include viewport--xb {
    display: inline-block;
    height: calc(100 * var(--vh, 1vh));
    // padding-bottom: 50px;
    width: 100%;
    overflow-y: auto;
  }
}
