.gallery {
  position: relative;

  .overflow {
    position: absolute;
    bottom: 11px;
    left: 102px;
    right: 122px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;

    font-size: 18px;
    line-height: 20px;
    font-weight: 700;
    font-family: $font-family--m;
    color: $color-default-white;
    letter-spacing: 0.25px;

    z-index: 2;

    @media (max-width: $viewport--lg - 1) {
      width: 495px;
      bottom: 35px;
      right: 0;
    }

    @media (max-width: 653px) and (min-width: 481px) {
      bottom: 35px;
      left: 50px;
      right: 0;
      width: 385px;
    }

    @media (max-width: $viewport--xs) {
      position: relative;
      left: 0;
      bottom: 0;
      min-height: 327px;
      width: 100%;

      flex-direction: column;
      justify-content: center;
      align-items: center;

      color: $color-night-rider-2;
    }
  }
}

.overflow__table-wrap {
  margin: 0;
  margin-right: 20px;
  padding: 0;

  display: flex;
  flex-direction: row;

  @media (max-width: $viewport--lg - 1) {
    margin-bottom: 15px;
  }

  @media (max-width: $viewport--xs) {
    position: relative;

    margin-right: 0;
    margin-top: -67px;
    padding: 25px 16px;
    padding-bottom: 5px;

    width: 266px;
    max-height: 182px;

    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex: 1 1 auto;

    border-radius: 6px;
    box-shadow: 1px 1px 10px 4px rgba(38, 21, 39, 0.2);
    background-color: $color-default-white;
    z-index: 20;

    &::before {
      position: absolute;
      content: "";
      top: 16px;
      left: 50%;
      width: 2px;
      height: 33%;
      background-color: $color-venice-blue;
    }

    &::after {
      position: absolute;
      content: "";
      bottom: 16px;
      left: 50%;
      width: 2px;
      height: 33%;
      background-color: $color-venice-blue;
    }
  }
}

.overflow__cell {
  min-width: 130px;
  min-height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 653px) and (min-width: 481px) {
    min-width: 98px;
    width: 98px;
  }

  @media (max-width: $viewport--xs) {
    min-width: 98px;
    width: 98px;

    font-family: $font-family--r;
  }
}

.overflow__cell-name {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 25px;
  font-weight: 500;

  @media (max-width: 653px) and (min-width: 481px) {
    font-size: 14px;
    line-height: 16px;
  }

  @media (max-width: $viewport--xs) {
    font-size: 16px;
    line-height: 18px;

    order: 2;
  }
}

.overflow__cell-data {
  margin: 0;
  padding: 0;
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;

  @media (max-width: 653px) and (min-width: 481px) {
    font-size: 21px;
    line-height: 26px;
  }

  @media (max-width: $viewport--xs) {
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;

    font-size: 21px;
    line-height: 26px;
    font-weight: 900;

    order: 1;
  }
}

.overflow__cell:nth-child(1) {
  @media (max-width: $viewport--xs) {
    text-transform: lowercase;

    position: relative;
    order: 1;
    margin-right: 34px;

    &::before {
      position: absolute;
      content: "";
      bottom: 4px;
      left: 10px;
      width: 92%;
      height: 2px;
      background-color: $color-venice-blue;
    }
  }
}

.overflow__cell:nth-child(2) {
  @media (max-width: $viewport--xs) {
    order: 3;
    margin-right: 34px;
  }
}

.overflow__cell:nth-child(3) {
  @media (max-width: $viewport--xs) {
    text-transform: lowercase;

    position: relative;
    order: 2;

    &::before {
      position: absolute;
      content: "";
      bottom: 4px;
      right: 10px;
      width: 92%;
      height: 2px;
      background-color: $color-venice-blue;
    }
  }
}

.overflow__cell:nth-child(4) {
  @media (max-width: $viewport--xs) {
    order: 4;
  }
}

.overflow__wrapper {
  display: flex;
  flex-direction: column;
}

.overflow__price-wrap {
  margin-top: -10px;
  margin-right: 26px;
  letter-spacing: 0.25px;

  @media (max-width: $viewport--xs) {
    margin-top: 25px;
    margin-right: 0;
    margin-bottom: 24px;
  }

  .overflow__price-flat {
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    min-width: 250px;
    text-align: center;

    font-size: 28px;
    line-height: 30px;
    color: $color-default-white;

    a {
      color: $color-default-white;
    }

    @media (max-width: 653px) and (min-width: 481px) {
      min-width: 140px;

      font-size: 22px;
      line-height: 24px;
      color: $color-default-white;
    }

    @media (max-width: $viewport--xs) {
      margin: 0;
      margin-bottom: 15px;

      font-size: 32px;
      line-height: 35px;
      color: $color-night-rider-2;

      a {
        color: $color-night-rider-2;
      }
    }
  }

  .overflow__price-meter {
    margin: 0;
    margin-left: -28px;
    padding: 0;
    min-width: 250px;
    text-align: center;

    font-size: 18px;
    line-height: 20px;
    font-weight: 500;

    @media (max-width: 653px) and (min-width: 481px) {
      min-width: 140px;

      font-size: 14px;
      line-height: 16px;
    }

    @media (max-width: $viewport--xs) {
      margin: 0;

      font-size: 25px;
      line-height: 29px;
    }
  }
}

.overflow__contact-wrap {
  margin-top: -14px;
  min-width: 216px;

  @media (max-width: 653px) and (min-width: 481px) {
    min-width: 140px;
  }

  @media (max-width: $viewport--xs) {
    margin: 0;
  }

  @media (max-width: $viewport--xs) {
    min-width: 312px;
    max-width: 100%;
  }

  .overflow__application {
    min-width: 100%;
    margin-bottom: 10px;
  }
}

.overflow__cell-desktop {
  @media (max-width: $viewport--xs) {
    display: none;
  }
}

.overflow__cell-mobile {
  display: none;

  @media (max-width: $viewport--xs) {
    display: block;
  }
}
