.buy-online {
  padding-top: 44px;
  padding-bottom: 34px;
  overflow: hidden;

  @include viewport--xs {
    padding-top: 7px;
  }
}

.buy-online__title {
  margin-bottom: 45px;

  @include viewport--xs {
    margin-bottom: 25px;
  }
}

.buy-online__block {
  flex-direction: column;
  align-items: center;
  overflow: visible;
  display: none;
  width: 100%;

  &--show {
    display: flex;
  }

  @include viewport--sm {
    padding-bottom: 33px;
  }

  & > .swiper-pagination {
    bottom: 0;
    display: none;

    @include viewport--sm {
      display: block;
    }

    .swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0 5px;
    }

    .swiper-pagination-bullet-active {
      transform: scale(1.6);
      background-color: $color-air-force-blue;
    }
  }
}

.buy-online__list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 17px;
  row-gap: 17px;
  list-style: none;
  padding-left: 0;
  margin: 0;

  @include viewport--md {
    grid-template-columns: repeat(3, 1fr);
  }

  @include viewport--sm {
    display: flex;
    column-gap: 0;
    row-gap: 0;
  }

  .catalog__column {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0;
    justify-self: center;
  }
}

.buy-online__btn {
  width: 289px;
  margin-top: 39px;
  padding-top: 10px;

  @include viewport--sm {
    display: none;
  }
}

.buy-online__tabs {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 35px;
  position: relative;

  .buy-online__tabs-wrap {
    margin-left: 0;
  }
}

.buy-online__tabs-wrap {
  width: auto;
  position: relative;

  @include viewport--xs {
    margin-bottom: 21px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $color-pattens;
    bottom: 0;
    left: 0;
  }
}

.buy-online__tab {
  padding: 0 6px 13px;
  margin-right: 27px;
  // margin-bottom: -2px;
  background-color: transparent;
  border: none;
  font-family: $font-family--m;
  color: $color-night-rider;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.016em;
  white-space: nowrap;
  cursor: pointer;
  position: relative;

  @include viewport--xs {
    margin-right: 7px;
    font-size: 16px;
  }

  &::before {
    content: "";
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: $color-air-force-blue;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &:not(.buy-online__tab--active) {
    &:hover {
      &::before {
        opacity: 0.5;
      }
    }
  }

  &--active {
    font-weight: bold;

    &::before {
      opacity: 1;
    }
  }

  &:focus {
    outline: none;
  }

  &:last-child {
    margin-right: 0;
  }
}

.buy-online__nav-btn {
  position: absolute;
  top: 0;
  left: -3px;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 30px;
  height: 30px;
  background: linear-gradient(90deg, $color-default-white, $color-default-white 50%, transparent);
  z-index: 1;
  cursor: pointer;

  &[aria-disabled="true"] {
    display: none;
  }

  svg {
    margin-bottom: 6px;
    transform: rotate(-90deg);
    fill: $color-blue-whale;
  }

  &--next {
    left: auto;
    right: -3px;
    justify-content: flex-end;
    background: linear-gradient(-90deg, $color-default-white, $color-default-white 50%, transparent);

    svg {
      transform: rotate(90deg);
    }
  }
}
