.banner-news {
  position: relative;
  width: 100%;
  min-height: 210px;
  margin-bottom: 35px;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 40px;
  padding-right: 40px;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: linear-gradient(rgba($color-default-white, 0.3), rgba($color-default-white, 0.3));
    z-index: -1;
  }

  img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    border-radius: 6px;
    object-fit: cover;
    object-position: 100%;
  }

  @media (max-width: $viewport--md) {
    margin-bottom: 0;

    img {
      object-position: 50%;
    }
  }

  @media (max-width: $viewport--sm) {
    padding-left: 25px;
    padding-right: 25px;
  }

  @media (max-width: $viewport--xs) {
    min-height: 210px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;

    img {
      object-position: 60%;
    }
  }

  &__head {
    margin: 0;
    margin-bottom: 19px;
    font-family: $font-family--m;
    font-size: 29px;
    line-height: 31px;
    font-weight: 500;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    @media (max-width: $viewport--xs) {
      margin-bottom: 12px;
      padding-right: 30px;
      line-height: 39px;
    }
  }

  &__slogan {
    max-width: 515px;
    width: 100%;
    margin: 0;
    margin-bottom: 6px;
    font-family: $font-family--m;
    font-size: 18px;
    line-height: 26px;
    font-weight: 500;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    @media (max-width: $viewport--xs) {
      margin-bottom: 10px;
      padding-right: 25px;
      line-height: 24px;
    }
  }

  form {
    display: flex;
    flex-direction: column;

    @media (max-width: $viewport--sm) {
      width: 100%;
    }
  }

  &__wrap-input {
    display: flex;

    @media (max-width: $viewport--sm) {
      display: block;
    }
  }

  [type="email"] {
    min-height: 42px;
    width: 260px;
    margin-right: 18px;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 12px;
    padding-right: 12px;
    border-radius: 6px;
    font-family: $font-family--r;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-night-rider;
    border: none;
    box-shadow: 0 0 0 1px $color-night-rider;
    background-color: transparent;
    transition: background-color $default-transition-settings, box-shadow $default-transition-settings;

    &:hover,
    &:focus {
      background-color: $color-default-white;
      box-shadow: none;
      outline: none;
    }

    @media (max-width: $viewport--sm) {
      width: 100%;
      margin-bottom: 11px;
    }

    &::placeholder {
      font-family: $font-family--r;
      font-size: 14px;
      min-height: 18px;
      line-height: 18px;
      font-weight: 400;
      letter-spacing: 0.025em;
      color: $color-dim-gray;
    }
  }

  [type="submit"] {
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 14px;
    font-weight: 700;

    @media (max-width: $viewport--sm) {
      width: 100%;
    }
  }

  &__consent {
    max-width: 700px;
    width: 100%;
    margin: 0;
    margin-top: 10px;
    font-family: $font-family--r;
    font-size: 15px;
    line-height: 19px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    a {
      color: $color-air-force-blue;
      transition: text-decoration $default-transition-settings;
    }

    a:hover,
    a:focus {
      text-decoration: underline;
    }

    @media (max-width: $viewport--sm) {
      order: -1;
      margin-bottom: 20px;
    }

    @media (max-width: $viewport--xs) {
      font-size: 12px;
      line-height: 12px;
    }
  }
}

.banner-news--head {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  min-height: auto;
  margin-bottom: 0;
  padding: 0;
  padding-bottom: 5px;
  border-bottom: 1px solid $color-dim-gray;

  &::after {
    display: none;
  }

  @media (max-width: $viewport--sm) {
    flex-wrap: wrap;
    padding-bottom: 0;
    border-bottom: none;
  }

  .banner-news__wrap-text {
    padding-top: 5px;
    width: 60%;
    padding-right: 20px;

    @media (max-width: $viewport--md) {
      width: 50%;
    }

    @media (max-width: $viewport--sm) {
      width: 100%;
    }
  }

  .banner-news__head {
    margin: 0;
    font-family: $font-family--r;
    font-size: 18px;
    line-height: 26px;
    font-weight: 500;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    @media (max-width: $viewport--xs) {
      margin-right: 60px;
      margin-bottom: 5px;
    }
  }

  .banner-news__slogan {
    max-width: none;
    margin: 0;
    font-family: $font-family--r;
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-dim-gray;

    @media (max-width: $viewport--sm) {
      margin-bottom: 27px;
    }

    @media (max-width: $viewport--xs) {
      line-height: 19px;
    }
  }

  form {
    display: flex;
    flex-direction: column;
    width: 490px;
    flex-shrink: 0;
    align-self: start;
    padding-right: 25px;

    @media (max-width: $viewport--md) {
      width: 50%;
      align-self: stretch;
      flex-shrink: 1;
    }

    @media (max-width: $viewport--sm) {
      width: 100%;
      padding-right: 0;
    }
  }

  .banner-news__wrap-input {
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;

    @media (max-width: $viewport--md) {
      flex-wrap: wrap;
    }

    @media (max-width: $viewport--md) {
      justify-content: space-between;
    }
  }

  .banner-news__wrap-input [type="email"] {
    display: block;
    max-width: 298px;
    width: 100%;
    margin-right: 21px;
    border-radius: 3px;
    flex-shrink: 1;
    min-height: 32px;
    padding-left: 36px;
    padding-right: 36px;
    padding-top: 7px;
    padding-bottom: 7px;
    font-family: $font-family--r;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-night-rider;
    border: none;
    box-shadow: none;
    background-color: $color-alice-blue;

    &:hover,
    &:focus {
      box-shadow: 0 0 0 1px $color-air-force-blue;
    }

    &:focus,
    &:active {
      outline: none;
    }

    @media (max-width: $viewport--md) {
      max-width: none;
      margin-bottom: 10px;
    }

    @media (max-width: $viewport--sm) {
      width: calc(100% - 160px);
      margin-right: 15px;
      margin-bottom: 0;
    }

    @media (max-width: $viewport--xs) {
      max-width: none;
      width: 100%;
      margin-right: 0;
      margin-bottom: 16px;
      padding-top: 12px;
      padding-left: 16px;
      padding-bottom: 14px;
    }

    &::placeholder {
      font-family: $font-family--r;
      font-size: 14px;
      min-height: 18px;
      line-height: 18px;
      font-weight: 400;
      letter-spacing: 0.025em;
      color: $color-dim-gray;
    }
  }

  .banner-news__btn {
    flex-shrink: 0;
    width: 135px;
    padding-left: 22px;
    padding-right: 22px;
    padding-top: 7px;
    padding-bottom: 7px;
    font-size: 14px;
    font-weight: 700;

    @media (max-width: $viewport--xs) {
      width: 100%;
      margin-left: 0;
      padding-top: 14px;
      padding-bottom: 14px;

      background-color: $color-venice-blue;
      color: $color-default-white;
      transition: box-shadow $default-transition-settings, background-color $default-transition-settings, color $default-transition-settings;

      &:hover,
      &:focus {
        color: $color-air-force-blue;
        background-color: $color-default-white;
        box-shadow: 0 0 0 1px $color-air-force-blue;
      }

      &:active {
        color: $color-air-force-blue;
        background-color: $color-alice-blue;
        box-shadow: none;
        outline: none;
      }
    }
  }

  .banner-news__close-btn {
    position: absolute;
    display: block;
    padding: 10px;
    right: -10px;
    top: -10px;
    width: 40px;
    height: 40px;
    border: 0;
    background-color: transparent;
    transition: all 0.3s ease;

    &:hover,
    &:focus {
      cursor: pointer;
      opacity: 0.7;
    }

    &:focus {
      outline: 1px solid $color-night-rider;
    }

    &:active {
      outline: none;
    }

    svg {
      display: block;
      width: 20px;
      height: 20px;
    }

    @media (max-width: $viewport--xs) {
      top: 0;
    }
  }

  .banner-news__consent {
    max-width: none;
    width: 100%;
    margin: 0;
    margin-top: 10px;
    font-family: $font-family--m;
    font-size: 10px;
    line-height: 11px;
    font-weight: 500;

    @media (max-width: $viewport--sm) {
      order: 0;
      margin-bottom: 0;
    }
  }
}
