.inner-sorter {
  display: flex;
  flex-direction: column;
  margin-bottom: 13px;

  &__column {
    display: flex;
    align-items: flex-end;

    font-family: $font-family--r;
    font-size: 18px;
    line-height: 26px;
    font-weight: 500;

    &:not(:last-child) {
      margin-bottom: 28px;
    }
  }

  &__find-offers {
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 26px;

    color: $color-eclipse;
  }
}
