.image {
  margin-bottom: 20px;

  img {
    border-radius: 3px;
    width: 100%;
    object-fit: cover;

    @media (max-width: $viewport--xs) {
      height: 249px;
    }
  }

  &--left {
    float: left;
    margin-right: 40px;

    @media (max-width: $viewport--sm) {
      margin-right: 20px;
    }

    @media (max-width: $viewport--xs) {
      float: none;
      margin-right: 0;
    }
  }

  &--right {
    float: right;
    margin-left: 40px;

    @media (max-width: $viewport--sm) {
      float: none;
      margin-left: 0;
    }
  }
}
