.info__container {
  max-width: 1366px;
  display: flex;
  flex-direction: column;
  padding: 36px 78px;
  margin: 0 auto;

  @media (max-width: $viewport--sm) {
    padding: 12px 20px;
  }
}

.info__text {
  color: $color-eclipse;
  font-family: $font-family--r;
  font-size: 13px;
  letter-spacing: 0.33px;
  text-align: center;
  margin: 0;
}

.info__links {
  display: flex;
  padding-top: 5px;
  margin-left: 23px;
  justify-content: center;
  flex-wrap: wrap;
}

.footer-link {

  color: $color-eclipse;
  font-family: $font-family--r;
  font-size: 13px;
  letter-spacing: 0.33px;
  line-height: 23px;
  padding-right: 30px;
  cursor: pointer;
  transition: all 0.3s;

  &:not(:last-of-type)::after {
    content: "";
    display: inline-block;
    width: 1px;
    height: 22px;
    border: 1px solid $color-dim-gray;
    position: absolute;
    margin-left: 15px;
  }

  &:focus,
  &:hover {
    color: $color-air-force-blue;
    transition: all 0.3s;
  }

  @media (max-width: $viewport--sm) {
    margin-bottom: 4px;
    padding-bottom: 3px;

    span {
      color: $color-allports;
      border-bottom: dotted 1px $color-allports;
      vertical-align: middle;
    }

    &::after {
      display: none!important;
    }
  }
}

.visible-md {
  display: none;
}

.info__links-entrance svg {
  display: none;
}

@media (max-width: $viewport--sm) {
  .info__text {
    text-align: left;
    padding-left: 0;
    font-size: 12px;
    line-height: 1.5;
  }

  .info__text::before {
    content: "";
    position: absolute;
    width: 89%;
    height: 1px;
    border: 1px solid $color-pink-swan;
    top: -5px;
  }

  .info__links {
    margin-left: 0;
    flex-direction: column;
    padding-top: 10px;
  }

  .info__links-entrance svg {
    display: inline-block;
    vertical-align: middle;
    margin-right: 7px;
  }

  .hide-md {
    display: none;
  }

  .visible-md {
    display: block;
  }
}
