.main-banner {
  padding-top: 33px;
  padding-bottom: 28px;

  @include viewport--sm {
    padding-top: 13px;
    padding-bottom: 11px;
  }
}

.main-banner__wrap {
  display: flex;
  border-radius: 8px;
  box-shadow: 0 0 9px 0 rgba($color-default-black, 0.16);
  overflow: hidden;

  @include viewport--sm {
    flex-direction: row-reverse;
  }
}

.main-banner__img {
  flex-shrink: 0;
  width: 43.3%;
  position: relative;

  @include viewport--sm {
    width: 51.9%;
    // height: 0;
    // padding-bottom: 0.444 * 55.2%;
  }

  &::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 50px;
    left: 0;
    top: 0;
    background: linear-gradient(90deg, $color-default-white, transparent);
    z-index: 1;

    @include viewport--sm {
      content: none;
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include viewport--sm {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.main-banner__text {
  padding: 35px 35px 35px 64px;
  flex-grow: 1;

  @include viewport--sm {
    padding: 12px;
  }
}

.main-banner__title {
  color: $color-venice-blue;
  font-size: 59px;
  line-height: 66px;
  margin-bottom: 9px;
  margin-top: 0;
  font-family: $font-family--m;
  letter-spacing: 1.2px;

  small {
    font-size: 57px;

    @include viewport--sm {
      font-size: 18px;
    }
  }

  span {
    font-size: 60px;

    @include viewport--sm {
      font-size: 33px;
      line-height: 48px;
      font-weight: 300;
    }
  }

  @include viewport--sm {
    font-size: 24px;
    line-height: 27px;
    margin-bottom: 29px;
    text-align: center;
    letter-spacing: 0.4px;

    span {
      display: block;
    }
  }

  @include viewport--xs {
    text-align: left;
  }
}

.main-banner__descr {
  font-size: 24px;
  line-height: 27px;
  margin-top: 0;
  margin-bottom: 25px;
  letter-spacing: -0.7px;
  font-family: $font-family--m;

  @include viewport--sm {
    display: none;
  }
}

.main-banner__btn {
  width: 253px;

  @include viewport--sm {
    padding: 6px;
    width: 100%;
    font-size: 12px;
    line-height: 16px;
  }
}
