.object-card {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 38px;

  background-color: $color-default-white;
  border: 2px solid $color-whisper;
  border-radius: 6px;
  transition: all 0.3s;

  &:last-child {
    margin-bottom: 33px;
  }

  &:hover,
  &:active {
    //box-shadow: 0 0 24px 0 #00000029;  .    187:  #00000029  rgba
    box-shadow: 0 0 24px 0 $color-box-shadow;
    transition: all 0.3s;
  }

  &__wrapper {
    display: flex;
    width: 84.6%;
    padding: 11px 0 13px 8px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    width: 45.5%;
    max-width: 457px;

    padding: 0 15px;
  }

  &__column {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    margin-bottom: 6px;

    &:last-child {
      margin-bottom: 14px;
    }

    p {
      margin: 0;
      margin-right: 10px;

      font-family: $font-family--r;
      font-size: 13px;
      line-height: 17px;
      font-weight: normal;
      letter-spacing: 0.02em;

      color: $color-night-rider;

      &:first-child {
        margin-right: 19px;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    svg {
      position: relative;
      top: 2px;

      margin-right: 5px;
    }

    a {
      font-family: $font-family--r;
      font-size: 13px;
      line-height: 17px;
      font-weight: normal;
      letter-spacing: 0.02em;
      transition: all 0.3s;

      color: $color-gray-2;

      &:hover {
        color: $color-air-force-blue;
        transition: all 0.3s;
      }
    }

    span {
      margin-right: 6px;

      width: 8px;
      height: 8px;

      background: $color-pink-swan;
      border-radius: 50%;
    }
  }

  &__title {
    margin: 15px 0;

    font-family: $font-family--r;
    font-weight: bold;
    font-size: 27px;
    line-height: 36px;
    letter-spacing: 0.042em;

    color: $color-eclipse;
  }

  &__list {
    padding: 0;
    margin: 0;

    display: flex;
    margin-bottom: 14px;

    list-style-type: none;
  }

  &__item {
    margin-right: 6px;
    color: $color-pink-swan;
    transition: all 0.3s;

    &:last-child {
      margin-right: 0;
    }

    &:hover,
    &:active,
    &--active {
      color: $color-air-force-blue;
      transition: all 0.3s;
    }

    a {
      color: inherit;
    }
  }

  &__price {
    display: flex;
    margin-bottom: 5px;

    font-family: $font-family--m;
    font-size: 23px;
    line-height: 30px;
    font-weight: bold;
    letter-spacing: 0.035em;
    color: $color-eclipse;

    p {
      margin: 0;
      margin-right: 5px;
    }

    span {
      font-family: $font-family--rubl;
      font-weight: normal;
    }
  }

  &__price-per-meter {
    margin: 0;
    margin-bottom: 6px;

    font-family: $font-family--m;
    font-size: 17px;
    line-height: 22px;
    font-weight: normal;
    letter-spacing: 0.026em;
    color: $color-gray-2;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;

    font-family: $font-family--r;
    font-weight: normal;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 0.023em;
    color: $color-night-rider;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
  }

  &__nav {
    display: flex;
    flex-wrap: wrap;

    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  &__nav-item {
    padding-right: 6px;
  }

  &__agent {
    width: 15.4%;
    max-width: 186px;
    margin-left: auto;
    padding: 22px 13px;

    background-color: $color-whisper;
  }
}

.object-card .checkbox-other {
  display: block;
  cursor: pointer;
  user-select: none;
  position: relative;
  z-index: 2;
  top: 3px;
  right: 0;
  left: 31px;
  transition: all 0.3s;
}

.object-card .checkbox-other input[type="checkbox"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

.object-card .checkbox-other span::before {
  content: "";
  width: 39px;
  height: 39px;
  position: absolute;
  left: -19px;
  top: 6px;
  background: url("../img/svg/like-object.svg") 50% 50% no-repeat;
  transition: all 0.3s;
}

.object-card .focused span::before {
  opacity: 0.8;
  transition: all 0.3s;
}

.object-card .checkbox-other span:hover::before {
  opacity: 0.8;
  transition: all 0.3s;
}

.object-card .checkbox-other input[type="checkbox"]:checked + span::before {
  width: 39px;
  height: 39px;
  background-image: url("../img/svg/favorite-empty.svg");
  transition: all 0.3s;
  border: 1px solid red;
  border-radius: 50%;
}
