.consult {
  display: flex;
  align-items: center;
  padding-top: 106px;
  padding-bottom: 102px;

  @include viewport--sm {
    flex-direction: column-reverse;
    padding-bottom: 47px;
    padding-top: 29px;
    overflow: hidden;
  }
}

.consult__content {
  flex-shrink: 0;
  width: 37.14%;
  margin-right: 18px;

  @include viewport--sm {
    width: 100%;
    margin-right: 0;
  }
}

.consult__title {
  margin-top: 0;
  margin-bottom: 42px;
  font-family: $font-family--m;
  font-size: 30px;
  line-height: 36px;

  @include viewport--xs {
    margin-bottom: 27px;
    font-size: 21px;
    line-height: 26px;
  }
}

.consult__text {
  max-width: 390px;
  margin-bottom: 11px;
  font-family: $font-family--m;
  font-size: 17px;
  line-height: 24px;

  @include viewport--xs {
    font-size: 14px;
    line-height: 17px;
  }

  p {
    margin: 0;
  }

  p + p {
    margin-top: 11px;
  }
}

.consult__socials {
  margin-top: 18px;
  margin-bottom: 26px;

  @include viewport--xs {
    margin-top: 17px;
    margin-bottom: 29px;
    padding-left: 6px;
  }
}

.consult__form {
  display: flex;
  flex-wrap: wrap;
}

.consult__col {
  width: calc(50% - 16px / 2);
  margin-right: 16px;
  margin-bottom: 25px;

  &:nth-child(2n) {
    margin-right: 0;
  }

  @include viewport--xs {
    width: 100%;
    margin-right: 0;
    margin-bottom: 12px;

    &::placeholder {
      color: $color-light-grey;
    }
  }
}

.consult__btn {
  width: 100%;

  @include viewport--xs {
    margin-top: 11px;
  }
}

.agreement-text {
  a {
    color: $color-air-force-blue;

    &:hover {
      text-decoration: underline;
    }
  }
}

.consult__small {
  margin-top: 12px;
  margin-bottom: 0;
  font-size: 13px;
  line-height: 16px;
  color: $color-eclipse;

  @include viewport--xs {
    margin-top: 23px;
    font-size: 14px;
  }
}

.consult__slider {
  justify-content: center;
  align-items: center;
  padding: 50px;

  @include viewport--sm {
    width: 100%;
  }

  @include viewport--xs {
    padding: 0;
    padding-top: 15px;
    overflow: visible;
    margin-bottom: 47px;
  }
}

.consult__wrap {
  align-items: center;
}

.consult__slide {
  opacity: 0;
  position: relative;
  width: 210px;
  height: 334px;
  flex-shrink: 0;
  padding: 10px 7px;
  // margin-left: 10px;
  border-radius: 6px;
  box-shadow: 0 0 9px 0 rgba($color-default-black, 0.16);
  background-color: $color-default-white;
  // transition: opacity 0.3s;

  @include viewport--xs {
    transform: scale(0.8);
    padding: 6px;
    padding-bottom: calc(95% + 6px * 2);
    height: 0;
    // height: auto;
  }

  &.swiper-slide-active {
    opacity: 1;
    transform: scale(1.2);
    z-index: 2;

    @include viewport--xs {
      transform: none;

      .consult__slide-text {
        display: block;
      }
    }
  }

  &.swiper-slide-next,
  &.swiper-slide-prev {
    opacity: 1;
    z-index: 1;
    transform: scale(1.1);

    @include viewport--xs {
      transform: scale(0.8);
    }
  }

  img {
    // position: absolute;
    // top: 0;
    // left: 0;
    border-radius: 6px;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include viewport--xs {
      position: absolute;

      width: calc(100% - 6px * 2);
      height: calc(100% - 6px * 2);
      top: 6px;
      left: 6px;
    }
  }
}

.consult__btns {
  @include viewport--xs {
    display: none;
  }
}

.consult__btns-to {
  position: absolute;
  z-index: 2;
  padding: 12px 21px;
  border: none;
  background-color: $color-default-white;
  border-radius: 6px;
  box-shadow: 0 0 9px 0 rgba($color-default-black, 0.16);
  font-size: 17px;
  line-height: 20px;
  font-family: $font-family--m;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;

  &:hover {
    background-color: $color-air-force-blue;
    color: $color-default-white;
  }

  &:focus {
    outline: none;
  }

  &--mortgage {
    left: 10px;
    bottom: 20px;
  }

  &--purchase {
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
  }

  &--services {
    right: 10px;
    bottom: 85px;
  }

  &--assessment {
    right: 10px;
    top: 63px;
  }

  @include viewport--xs {
    top: 0;
    left: 50%;
    bottom: auto;
    right: auto;
  }
}

.consult__nav {
  display: none;
  width: 34px;
  height: 40px;
  // display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;

  @include viewport--xs {
    display: flex;
  }

  svg {
    transform: rotate(-90deg);
    fill: $color-default-white;
  }

  &--next {
    right: 16.1%;

    svg {
      transform: rotate(90deg);
    }
  }

  &--prev {
    left: 8px;
  }
}

.consult__slide-text {
  display: none;

  @include viewport--xs {
    display: block;
    position: absolute;
    top: -13px;
    right: 7px;
    z-index: 1;
    margin: 0;
    padding: 8px 15px 9px;
    background-color: $color-default-white;
    border-radius: 6px;
    font-family: $font-family--m;
    font-size: 14px;
    line-height: 18px;
    font-weight: 600;
    box-shadow: 0 0 9px 0 rgba($color-default-black, 0.16);
  }
}
