.description {
  margin-bottom: 23px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @include viewport--xs {
    margin-bottom: 10px;
    flex-direction: column;

    line-height: 20px;
  }
}

.description__text {
  max-width: 846px;
  padding-right: 20px;
  margin-bottom: 40px;

  font-size: 16px;
  line-height: 22px;
  font-family: $font-family--m;
  color: $color-night-rider-2;
  letter-spacing: 0.25px;

  @media (max-width: $viewport--lg - 1) {
    max-width: 100%;
    margin-bottom: 0;
  }

  @media (min-width: 1343px) {
    max-width: 100%;
    margin: 0;
  }

  @include viewport--xs {
    margin-bottom: 10px;

    display: flex;
    flex-direction: column;

    line-height: 22px;
  }

  p {
    margin: 0;
    padding: 0;
    margin-bottom: 23px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.description__wrapper {
  margin-top: -10px;
  margin-right: 45px;
}
