.breadcrumb {
  &__list {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
  }

  &__item {
    position: relative;
    padding-right: 20px;
    font-family: $font-family--r;
    font-weight: normal;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 0.023em;
    color: $color-pink-swan;

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 6px;
      transform: translateY(-55%);
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $color-pink-swan;
    }

    @media (max-width: $viewport--sm) {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &__link {
    color: $color-pink-swan;
    transition: all 0.3s ease;

    &:hover {
      color: $color-venice-blue;
    }
  }

  &__link-current {
    color: $color-venice-blue;
  }
}

.breadcrumb-padding .breadcrumb {
  @media(min-width: 1101px) {
    padding-right: 150px;
  }
}
