.gallery {
  display: flex;

  @media (max-width: $viewport--xs) {
    flex-direction: column;

    color: $color-black-2;
  }
}

.gallery__container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 78px;
  margin-bottom: 57px;

  @media (max-width: $viewport--md) {
    padding: 0 60px;
  }

  @media (max-width: $viewport--sm) {
    padding: 0 40px;
  }

  @media (max-width: $viewport--xs) {
    margin-bottom: 22px;
    padding: 0;
  }
}

.gallery__image {
  min-height: 0;

  @media (max-width: $viewport--xs) {
    min-height: 433px;
  }
}

.gallery__image-overflow {
  position: relative;
  width: 100%;
  height: 480px;
  margin: 0 auto;

  @media (max-width: $viewport--xs) {
    height: 433px;
  }

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: "";
    z-index: 2;
    background: linear-gradient(180deg, rgba($color-default-white, 0.1) 0%, rgba($color-default-black, 0.5) 100%);
  }

  img {
    width: 100%;
    height: 480px;
    object-fit: cover;

    @media (max-width: $viewport--xs) {
      height: 433px;
    }
  }
}

.gallery .swiper-container {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.gallery .swiper-button-next {
  width: 40px;
  height: 40px;
  top: 52%;
  right: 38px;
  outline: none;

  transition: opacity $default-transition-settings;

  @media (max-width: $viewport--xs) {
    right: 22px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }
}

.gallery .swiper-button-prev {
  width: 40px;
  height: 40px;
  top: 52%;
  left: 38px;
  outline: none;

  transition: opacity $default-transition-settings;

  @media (max-width: $viewport--xs) {
    left: 22px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }
}

.gallery .swiper-button-next::after {
  width: 40px;
  height: 40px;
  font-size: 0;
  background-image: url("../img/svg/arrow-gallery.svg");
  background-repeat: no-repeat;
}

.gallery .swiper-button-prev::after {
  width: 40px;
  height: 40px;
  font-size: 0;
  transform: rotate(180deg);
  background-image: url("../img/svg/arrow-gallery.svg");
  background-repeat: no-repeat;
}

.gallery .swiper-button-next.swiper-button-disabled,
.gallery .swiper-button-prev.swiper-button-disabled {
  opacity: 1;
}
