@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin viewport--md {
  @media (max-width: $viewport--lg - 1) {
    @content;
  }
}

//  Desktop  Mobile

@mixin viewport--bg {
  @media (max-width: $viewport--lg - 1) and (min-width: $viewport--xs - 1) {
    @content;
  }
}

// Tablet first

@mixin viewport--sm {
  @media (max-width: $viewport--md - 1) {
    @content;
  }
}

// Mobile first

@mixin viewport--xs {
  @media (max-width: $viewport--sm - 1) {
    @content;
  }
}

//   

@mixin viewport--xb {
  @media (max-width: 932px) {
    @content;
  }
}
