.footer {
  min-height: 859px;
}

.footer__communication {
  background-color: $color-snow;
  position: relative;
  z-index: 1;
  border: none;
}

.footer__communication::before {
  content: "";
  position: absolute;
  width: 100%;
  z-index: -1;
  background: linear-gradient(to right, $color-snow 83.8%, $color-snow 29.2%);
}

.footer__logo {
  width: 124px;
  height: 56px;
  margin-right: 32px;
}

.footer__links {
  background-color: $color-whisper;
  position: relative;
  z-index: 1;
  border: none;
}

.footer__info {
  background-color: $color-whisper;
  position: relative;
  z-index: 1;
  border: none;
}

.footer__map {
  position: relative;
  padding: 74px 0;
}

.footer__links::before,
.footer__info::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  top: 0;
  width: 100%;
  z-index: -1;
  background: linear-gradient(to right, $color-whisper 83.8%, $color-whisper 29.2%);
}

@media (max-width: $viewport--sm) {
  .footer {
    display: flex;
    flex-direction: column;
  }

  .footer__logo {
    width: 95px;
    height: 41px;
    margin-right: 0;
  }

  .footer__links {
    flex-direction: row-reverse;
    order: 1;
  }

  .footer__communication {
    justify-content: space-between;
    background-color: $color-whisper;
  }

  .footer__info {
    order: 1;
  }
}
