.communication {
  padding-top: 40px;
  padding-bottom: 20px;
}

.communication__wrapper {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 78px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.communication__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.communication__tel {
  display: flex;
  flex-direction: column;
  padding-right: 58px;
}

.communication__tel-mask {
  width: 169px;
  font-family: $font-family--r;
  line-height: 29px;
  text-align: left;
  margin: 0;
}

.communication__tel-number {
  color: $color-venice-blue;
  font-family: $font-family--r;
  font-size: 18px;
  font-weight: 900;
  line-height: 24px;
  transition: all 0.3s;
}

.communication__tel-number:hover,
.communication__tel-number:focus {
  opacity: 0.5;
  transition: all 0.3s;
}

.communication__social {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  flex-grow: 1;
  justify-content: flex-end;
  padding-top: 6px;
}

.communication__social a {
  display: block;
  cursor: pointer;
}

@media (max-width: $viewport--md) {
  .communication__wrapper {
    padding: 0 60px;
  }

  .communication__social {
    justify-content: flex-start;
  }
}

@media (max-width: $viewport--sm) {
  .communication__wrapper {
    justify-content: space-between;
    background-color: $color-whisper;
    padding: 0 20px;
  }

  .communication__list {
    margin: 0;
    padding: 0;
  }

  .communication__tel {
    margin-left: 1px;
    margin-right: 0;
    padding: 0;
  }

  .communication__tel:nth-child(2) {
    display: none;
  }

  .communication__social {
    flex-grow: inherit;
    padding-top: 26px;
  }
}
