.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 30px;
}

.catalog__block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 65px;
}

.catalog__description {
  width: 100%;
  padding: 21px 22px 18px 26px;
}

.catalog__column {
  width: calc(22.5% - -16px);
  min-height: 345px;
  margin-bottom: 25px;
  //box-shadow: 0 0 13px $color-pink-swan;
  border-radius: 15px;

  box-shadow: 0 0 4px $color-box-shadow;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: 0 0 18px $color-box-shadow;
  }
}

.catalog__container {
  position: relative;
  height: 177px;
}

.catalog__card {
  position: relative;
}

.catalog__card img {
  width: 100%;
  height: 177px;
  border-radius: 15px 15px 0 0;
  object-fit: cover;
}

.catalog__show {
  position: absolute;
  display: block;
  width: 108px;
  height: 26px;
  font-weight: 500;
  box-shadow: -2px 1px 9px rgba(0, 3, 6, 0.39);
  border-radius: 4px;
  background-color: #3ba22f;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  color: $color-white;
  line-height: 27px;
  border: 0;
  z-index: 1;
  text-align: center;
  top: 18px;
  left: -5px;
  padding: 0;
}

.catalog__show:focus,
.catalog__show:hover {
  opacity: 0.7;
  border: none;
  background-color: #3ba22f;
  transition: all 0.3s;
}

.catalog__price {
  margin: 0;
  color: $color-air-force-blue;
  font-size: 33px;
  font-weight: 700;
  text-align: center;
  font-style: normal;
  letter-spacing: normal;
  line-height: normal;
  padding-bottom: 7px;
}

.catalog__address {
  color: $color-night-rider-2;
  font-size: 17px;
  font-weight: 300;
  font-style: normal;
  letter-spacing: normal;
  line-height: normal;
  min-height: 40px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: inherit;
}

.catalog__item-list sup {
  left: 40px;
  font-size: 48%;
  top: -2.5em;
}

.catalog__item {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  color: $color-night-rider-2;
  font-size: 22px;
  font-weight: 900;
  line-height: 43px;
  margin-left: -12px;
  margin-right: -12px;
}

.catalog__item-list {
  width: 33%;
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
}

.catalog__item-list:not(:first-of-type)::after {
  content: "";
  display: inline-block;
  width: 1px;
  height: 48px;
  border: 1px solid $color-pink-swan;
  position: absolute;
  margin-top: 13px;
  left: 1px;
}

.catalog__item-list span {
  color: $color-black-2;
  font-family: $font-family--r;
  font-size: 17px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.42px;
  line-height: 23px;
  text-align: center;
}

.catalog_like {
  width: 46px;
  height: 46px;
  position: absolute;
  z-index: 1;
  left: 89%;
  top: 10px;
}

.checkbox-other {
  display: block;
  cursor: pointer;
  user-select: none;
  position: absolute;
  z-index: 1;
  top: 6px;
  right: 41px;
}

.checkbox-other input[type="checkbox"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

.checkbox-other span::before {
  content: "";
  width: 46px;
  height: 46px;
  position: absolute;
  left: -19px;
  top: 6px;
  background: url("../img/sprite/icon-like.svg") 50% 50% no-repeat;
  transition: all 0.3s;
}

.focused span::before {
  opacity: 0.8;
  transition: all 0.3s;
}

.checkbox-other span:hover::before {
  opacity: 0.8;
  transition: all 0.3s;
}

.checkbox-other input[type="checkbox"]:checked + span::before {
  width: 46px;
  height: 46px;
  background-image: url("../img/svg/icon-like-2.svg");
  transition: all 0.3s;
}

.catalog-swiper__swiper-container {
  width: 100%;
  height: 100%;
  margin: 0;
  position: initial;
}

.catalog__swiper .swiper-wrapper {
  width: 292px;
  margin: 0;
  z-index: 0;
}

.catalog__swiper .swiper-pagination {
  width: 292px;
  display: flex;
  justify-content: space-between;
  z-index: initial;
  position: sticky;
  bottom: 2px;
}

.catalog__swiper .swiper-pagination-bullet {
  width: 24%;
  height: 1px;
  border: 2px solid $color-light-grey;
  border-radius: 0;
  opacity: 1;
  background: initial;
  transition: all 0.3s;
}

.catalog__swiper .swiper-pagination-bullet-active {
  border: 2px solid $color-allports;
  transition: all 0.3s;
}

.catalog__swiper .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0;
}

.catalog__swiper .swiper-pagination-bullet:hover,
.catalog__swiper .swiper-pagination-bullet:focus,
.catalog__swiper .swiper-pagination-bullet-active:focus,
.catalog__swiper .swiper-pagination-bullet-active:hover {
  opacity: 0.7;
  transition: all 0.3s;
}

@media (max-width: 1289px) {
  .catalog__column {
    width: calc(33% - 15px);
  }

  .catalog__swiper .swiper-pagination {
    width: initial;
  }

  .catalog__item-list {
    width: 33%;
  }

  .catalog__item-list:not(:first-of-type)::after {
    margin-left: 0;
  }
}

@media (max-width: $viewport--md) {
  .catalog__column {
    width: calc(49% - 3px);
  }
}

@media (max-width: $viewport--sm) {
  .catalog__column {
    width: 100%;
  }
}

@media (max-width: 360px) {
  .catalog__column {
    width: 292px;
  }
}
