@mixin history-timeline-mimic-border($direction: hor, $color: $color-air-force-blue) {
  background-image: radial-gradient(circle closest-side, $color calc(100% - 0.5px), transparent 100%);
  background-position: 0 0, 100% 0, 0 100%;

  @if ($direction == hor) {
    background-repeat: repeat-x;
    background-size: 7px 4px, 4px 7px;
  }

  @if ($direction == vert) {
    background-repeat: repeat-y;
    background-size: 4px 7px, 7px 4px;
  }
}

.history-timeline {
  position: relative;
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 217px;

  &::before {
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 4px;

    @include history-timeline-mimic-border(hor);

    transform: translateY(-50%);

    content: "";
  }

  //     @media,      
  &__logo {
    position: relative;

    display: none;
    align-items: center;
    justify-content: center;
    width: 96px;
    height: 96px;

    //border: 2px solid #4d80a0;
    border: 2px solid $color-air-force-blue;
    border-radius: 50%;

    background-color: $color-white;

    &::after {
      position: absolute;
      top: 50%;
      left: 50%;

      width: 95%;
      height: 95%;

      //border: 1px solid #4d80a0;
      border: 1px solid $color-air-force-blue;
      border-radius: 50%;

      transform: translateX(-50%) translateY(-50%);

      content: "";
    }
  }
}

@include viewport--xb {
  .history-timeline {
    flex-direction: column;
    width: 95%;
    margin-right: auto;
    margin-left: auto;
    padding-bottom: 25px;

    &::before {
      top: 0;
      bottom: 0;
      left: 50%;
      right: unset;

      width: 4px;
      height: 100%;

      @include history-timeline-mimic-border(vert);

      transform: translateX(-50%);
    }

    &::after {
      position: absolute;
      left: 50%;
      bottom: 0;

      width: 23px;
      height: 23px;

      //background-color: #4d80a0;
      background-color: $color-air-force-blue;

      border-radius: 50%;

      transform: translateX(-50%);

      content: "";
    }

    &__logo {
      display: flex;
      margin-bottom: 18px;
    }
  }
}

@import "./history-timeline-milestone";
@import "./history-timeline-year";
