.sorting-by-quantity {
  display: flex;
  align-items: flex-end;
  margin-right: 28px;
  margin-left: auto;

  span {
    font-family: $font-family--m;
    font-size: 18px;
    line-height: 18px;
    font-weight: bold;
  }

  &__list {
    display: flex;

    padding: 0;
    margin: 0;
    margin-left: 12px;
    list-style-type: none;
  }

  &__link {
    padding: 13px 17px;
    font-size: 14px;
    line-height: 14px;

    color: $color-gray-2;
    border-bottom: 2px solid $color-gray-2;

    &:hover,
    &:focus {
      color: $color-air-force-blue;
      border-bottom: 2px solid $color-air-force-blue;
      transition: $default-transition-settings;
    }
  }

  &__item {
    margin-right: 3px;
    min-height: 38px;

    &--active {
      .sorting-by-quantity__link {
        color: $color-air-force-blue;
        border-bottom: 2px solid $color-air-force-blue;
      }
    }
  }
}
