.reviews {
  padding-bottom: 30px;

  @media (max-width: $viewport--xs) {
    padding-bottom: 5px;
  }

  .h2 {
    margin-bottom: 48px;

    @media (max-width: $viewport--xs) {
      margin-bottom: 30px;
    }
  }

  &__swiper.swiper-container {
    overflow: visible;
    padding-left: 30px;
    padding-right: 30px;

    @media (max-width: $viewport--md) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__list-reviews {
    display: flex;
    flex-wrap: nowrap;
    height: auto;
    margin: 0;
    margin-bottom: 43px;
    padding-left: 0;
    list-style: none;

    @media (max-width: $viewport--xs) {
      margin-bottom: 36px;
    }
  }

  &__item-reviews {
    width: 372px;
    min-width: 315px;
    min-height: 242px;
    height: auto;
    flex-shrink: 0;
    padding: 20px 25px 28px 25px;
    opacity: 0.8;
    border-radius: 6px;
    box-shadow: 0 0 4px $color-box-shadow;
    transition: all 0.3s ease;

    &:hover {
      box-shadow: 0 0 18px $color-box-shadow;
    }

    @media (max-width: $viewport--xs) {
      padding: 17px 22px 24px 22px;
    }
  }

  &__item-reviews.swiper-slide-active,
  &__item-reviews.swiper-slide-prev,
  &__item-reviews.swiper-slide-next {
    opacity: 1;
  }

  &__person {
    display: flex;
    margin-bottom: 16px;

    @media (max-width: $viewport--xs) {
      margin-bottom: 15px;
    }

    img {
      min-width: 55px;
      display: block;
      border-radius: 50%;
      width: 55px;
      height: 55px;
      object-fit: cover;
      margin-left: 2px;

      @media (max-width: $viewport--xs) {
        min-width: 44px;
        width: 44px;
        height: 44px;
        margin-left: 4px;
      }
    }
  }

  &__info-person {
    display: flex;
    flex-direction: column;
    margin-left: 12px;

    @media (max-width: $viewport--xs) {
      margin-left: 17px;
    }

    p {
      margin: 0;
      font-family: $font-family--m;
      font-size: 19px;
      line-height: 29px;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: $color-air-force-blue;

      @media (max-width: $viewport--xs) {
        font-size: 16px;
        line-height: 24px;
      }
    }

    time {
      font-family: $font-family--m;
      font-size: 15px;
      line-height: 15px;
      font-weight: 500;
      letter-spacing: 0.025em;
      color: $color-default-black;

      @media (max-width: $viewport--xs) {
        font-size: 12px;
        line-height: 12px;
      }
    }
  }

  &__text-reviews {
    margin: 0;
    margin-bottom: 26px;
    font-family: $font-family--m;
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    max-height: 10 * 20px * 16;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 10;
    overflow: hidden;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    @media (max-width: $viewport--xs) {
      margin-bottom: 16px;
      font-size: 14px;
      line-height: 16px;
    }
  }

  &__stars {
    display: flex;
    flex-wrap: wrap;
    margin-left: 12px;

    svg {
      margin-right: 5px;
      fill: $color-gray-3;

      @media (max-width: $viewport--xs) {
        width: 14px;
        height: 14px;
      }
    }

    .reviews__star-choosed {
      fill: #ffc107;
    }
  }

  &__swiper-controls {
    display: flex;
    width: 214px;
    margin-left: auto;
    margin-right: auto;
    min-height: 30px;
    padding-right: 56px;
    align-items: center;
    justify-content: space-between;

    @media (max-width: $viewport--xs) {
      width: 143px;
      padding-right: 9px;
      min-height: 27px;
    }
  }

  &__pagination.swiper-pagination-fraction {
    position: static;
    min-height: 30px;
    font-family: $font-family--m;
    font-size: 25px;
    line-height: 30px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-venice-blue;

    @media (max-width: $viewport--xs) {
      font-size: 21px;
      line-height: 27px;
    }

    span:first-child {
      margin-right: -8px;
    }

    span:last-child {
      margin-left: -5px;
    }
  }

  &__button-prev.swiper-button-prev,
  &__button-next.swiper-button-next {
    position: static;
    width: 35px;
    height: 25px;
    margin-bottom: -20px;
    transition: all 0.3s ease;

    @media (max-width: $viewport--sm) {
      outline: $color-transparent;
      -webkit-tap-highlight-color: $color-transparent;
    }

    &:hover {
      opacity: 0.7;
    }

    @media (max-width: $viewport--xs) {
      svg {
        width: 27px;
        height: 21px;
      }
    }
  }

  &__button-prev.swiper-button-prev::after,
  &__button-next.swiper-button-next::after {
    display: none;
  }
}
