.piece-news {
  padding-bottom: 15px;

  .h1 {
    margin-top: 35px;
    margin-bottom: 17px;

    @media (max-width: $viewport--xs) {
      margin-top: 10px;
      margin-bottom: 18px;
    }

    span {
      display: block;
      margin: 0;
      font-family: $font-family--m;
      font-size: 30px;
      line-height: 57px;
      font-weight: 400;
      letter-spacing: 0.02em;
      color: $color-night-rider;

      @media (max-width: $viewport--xs) {
        margin-top: 14px;
        font-size: 21px;
        line-height: 33px;
      }
    }
  }

  p {
    margin: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: $font-family--m;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-night-rider;
  }

  &__large {
    width: 100%;
  }

  &__middle {
    width: 56%;

    @media (max-width: $viewport--sm) {
      width: 100%;
    }
  }

  &__small {
    width: 36%;

    @media (max-width: $viewport--md) {
      width: 50%;
    }

    @media (max-width: $viewport--xs) {
      width: 100%;
    }
  }
}
