.news-board {
  &__grid {
    list-style: none;
    margin: 0;
    padding-left: 0;
    padding-bottom: 52px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 21.4%;
    gap: 32px 18px;

    @media (max-width: $viewport--md) {
      grid-template-columns: 1fr 1fr 1fr;
    }

    @media (max-width: $viewport--sm) {
      grid-template-columns: 1fr 1fr;
    }

    @media (max-width: $viewport--xs) {
      grid-template-columns: 1fr;
      gap: 30px 0;
      padding-bottom: 30px;
    }
  }

  &__news-item {
    border-radius: 6px;
  }

  &__news-item .news-board__news-link {
    display: block;
  }

  &__news-item--large-img {
    position: relative;

    img {
      width: 100%;
      height: 426px;
      border-radius: 6px;
      object-fit: cover;

      @media (max-width: $viewport--sm) {
        height: 300px;
      }

      @media (max-width: $viewport--xs) {
        height: 249px;
      }
    }
  }

  &__news-item__large-first-layout {
    grid-row: 1 / 2;
    grid-column: 1 / 4;

    @media (max-width: $viewport--md) {
      grid-row: 1 / 2;
      grid-column: 1 / 4;
    }

    @media (max-width: $viewport--sm) {
      grid-row: 1 / 2;
      grid-column: 1 / 3;
    }

    @media (max-width: $viewport--xs) {
      grid-row: 1 / 2;
    }
  }

  &__news-item__large-second-layout {
    grid-row: 3 / 4;
    grid-column: 2 / 5;

    @media (max-width: $viewport--md) {
      grid-row: 5 / 6;
      grid-column: 2 / 4;
    }

    @include viewport--xb {
      grid-row: 6 / 7;
      grid-column: 1 / 4;
    }

    @media (max-width: $viewport--sm) {
      grid-row: 6 / 7;
      grid-column: 1 / 3;
    }

    @media (max-width: $viewport--xs) {
      grid-row: 6 / 7;
    }
  }

  &__news-big {
    position: absolute;
    max-width: 630px;
    width: 100%;
    padding-top: 43px;
    padding-bottom: 43px;
    padding-left: 60px;
    padding-right: 60px;
    left: 0;
    bottom: 0;

    @media (max-width: $viewport--sm) {
      padding-top: 14px;
      padding-bottom: 14px;
      padding-left: 16px;
      padding-right: 16px;
    }
  }

  &__news-head {
    margin: 0;
    margin-bottom: 11px;
    font-family: $font-family--m;
    font-size: 30px;
    line-height: 34px;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: $color-default-white;
    transition: all 0.3s ease;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;

    @media (max-width: $viewport--sm) {
      margin-bottom: 7px;
      font-size: 18px;
      line-height: 23px;
    }
  }

  &__news-subhead {
    margin: 0;
    font-family: $font-family--m;
    font-size: 20px;
    line-height: 27px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-default-white;
    transition: all 0.3s ease;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;

    @media (max-width: $viewport--sm) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  .news-board__news-item--large-img a:hover p {
    cursor: pointer;
    opacity: 0.7;
  }

  &__text-layout {
    grid-row: 1 / 3;
    grid-column: 4 / 5;

    @media (max-width: $viewport--md) {
      grid-row: 2 / 4;
      grid-column: 3 / 4;
    }

    @include viewport--xb {
      grid-row: 2 / 4;
      grid-column: 2 / 4;
    }

    @media (max-width: $viewport--sm) {
      grid-row: 2 / 6;
      grid-column: 2 / 3;
    }

    @media (max-width: $viewport--xs) {
      grid-row: 2 / 3;
      grid-column: 1 / 1;
    }
  }

  &__news-item--text {
    padding-left: 12px;

    @media (max-width: $viewport--xs) {
      padding-left: 0;
      padding-right: 40px;
    }
  }

  &__head-brief {
    margin: 0;
    margin-bottom: 14px;
    font-family: $font-family--m;
    font-size: 20px;
    line-height: 25px;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: $color-night-rider;

    @media (max-width: $viewport--xs) {
      margin-bottom: 12px;
    }
  }

  &__list-brief {
    list-style: none;
    margin: 0;
    padding-left: 0;
  }

  &__item-brief {
    margin-bottom: 25px;
    transition: all 0.3s ease;

    &:hover p {
      color: $color-denim;
    }
  }

  &__item-brief p {
    margin: 0;
    margin-bottom: 11px;
    font-family: $font-family--m;
    font-size: 16px;
    line-height: 18px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: color-night-rider;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;

    @media (max-width: $viewport--xs) {
      font-size: 18px;
      line-height: 22px;
    }
  }

  &__item-brief time {
    display: block;
    font-family: $font-family--r;
    font-size: 15px;
    line-height: 21px;
    font-weight: 500;
    letter-spacing: 0.025em;
    color: $color-denim;

    @media (max-width: $viewport--xs) {
      font-size: 17px;
    }
  }

  &__banner-layout {
    grid-row: 4 / 5;
    grid-column: 1 / 5;

    @media (max-width: $viewport--md) {
      grid-row: 7 / 8;
      grid-column: 1 / 4;
    }

    @include viewport--xb {
      grid-row: 7 / 8;
      grid-column: 1 / 4;
    }

    @media (max-width: $viewport--sm) {
      grid-row: 7 / 8;
      grid-column: 1 / 3;
    }

    @media (max-width: $viewport--xs) {
      grid-row: 8 / 9;
    }
  }

  &__middle-layout {
    grid-row: 5 / 6;
    grid-column: 3 / 5;

    @media (max-width: $viewport--md) {
      grid-row: 4 / 5;
      grid-column: 2 / 4;
    }

    @include viewport--xb {
      grid-row: 5 / 6;
      grid-column: 2 / 4;
    }

    @media (max-width: $viewport--sm) {
      grid-row: 9 / 10;
      grid-column: 1 / 3;
    }

    @media (max-width: $viewport--xs) {
      grid-row: 7 / 8;
    }
  }

  &__news-item--unicellular {
    img {
      margin-bottom: 24px;
      border-radius: 6px;
      width: 100%;
      height: 209px;
      object-fit: cover;

      @media (max-width: $viewport--xs) {
        height: 219px;
      }
    }

    p {
      margin: 0;
      margin-bottom: 10px;
      font-family: $font-family--m;
      font-size: 20px;
      line-height: 25px;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: $color-night-rider;
      transition: all 0.3s ease;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;

      @media (max-width: $viewport--xs) {
        margin-bottom: 3px;
      }
    }

    &:hover p {
      opacity: 0.7;
    }
  }

  &__unicellular-layout {
    @media (max-width: $viewport--xs) {
      grid-column: 1 / 1;
    }
  }

  &__time-layout {
    @media (max-width: $viewport--md) {
      grid-row: 5 / 6;
      grid-column: 1 / 2;
    }

    @include viewport--xb {
      grid-row: 5 / 6;
      grid-column: 1 / 2;
    }

    @media (max-width: $viewport--sm) {
      grid-row: 8 / 9;
      grid-column: 2 / 3;
    }

    @media (max-width: $viewport--xs) {
      grid-column: 1 / 1;
    }
  }

  &__none {
    @media (max-width: $viewport--xs) {
      display: none;
    }
  }

  &__news-item--time {
    img {
      border-radius: 6px;
      margin-bottom: 12px;
      width: 100%;
      height: 317px;
      object-fit: cover;

      @include viewport--xb {
        width: 100%;
        height: 209px;
      }
    }

    time {
      display: block;
      margin-bottom: 8px;
      font-family: $font-family--r;
      font-size: 17px;
      line-height: 28px;
      font-weight: 500;
      letter-spacing: 0.025em;
      color: $color-denim;
    }
  }
}
