.hero {
  padding-bottom: 65px;

  @media (max-width: $viewport--sm) {
    padding-bottom: 62px;
  }

  @media (max-width: $viewport--xs) {
    position: relative;
    min-height: 358px;
  }

  .h1 {
    margin-bottom: 23px;

    @media (max-width: $viewport--xs) {
      margin-bottom: 16px;
    }
  }

  &__wrap {
    position: relative;
    width: 100%;
    padding-bottom: 32.17%;
    margin-left: 3px;
    margin-right: 3px;
    padding-left: 96px;
    padding-right: 90px;
    background-color: $color-air-force-blue;
    background-image: url("../img/bg/about-company.jpg");
    background-repeat: no-repeat;
    background-size: 100% auto;

    @mixin retina {
      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/bg/about-company@2x.jpg");
        background-size: 1205px 387px;
      }
    }

    @media (max-width: $viewport--xs) {
      position: absolute;
      width: 100%;
      left: 0;
      right: 0;
      border-radius: 6px;
      padding-bottom: 0;
      min-height: 248px;
      margin-left: 0;
      margin-right: 0;
      padding-left: 0;
      padding-right: 0;
      background-image: url("../img/bg/about-company-mobile.jpg");
      background-position: 50% 0;
      background-repeat: no-repeat;
      background-size: cover;

      @mixin retina {
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
          background-image: url("../img/bg/about-company-mobile@2x.jpg");
          background-size: cover;
        }
      }
    }
  }

  &__navigation.swiper-container {
    position: absolute;
    left: 7.85%;
    right: 7.48%;
    bottom: 13%;
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 1px;
    padding-right: 1px;

    @media (max-width: $viewport--md) {
      left: 5%;
      right: 5%;
    }

    @media (max-width: $viewport--sm) {
      left: 0;
      right: 0;
      bottom: -48px;
      overflow: visible;
      padding-left: 0;
      padding-right: 0;

      &::before {
        content: "";
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        background-color: $color-pattens;
      }
    }

    @media (max-width: $viewport--xs) {
      left: 20px;
      right: 20px;
      overflow: visible;
    }
  }

  &__navigation-wrap.swiper-wrapper {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: calc(100% + (100% - 696px) / 7);
    margin: 0;
    margin-left: calc((696px - 100%) / 7);
    padding-left: 0;
    list-style: none;

    @media (max-width: $viewport--md) {
      width: calc(100% + (100% - 519px) / 7);
      margin-left: calc((519px - 100%) / 7);
    }

    @media (max-width: $viewport--sm) {
      width: 100%;
      margin-left: 0;
      min-height: 37px;
      flex-wrap: nowrap;
      align-items: center;
      justify-content: flex-start;
    }
  }

  &__item-navigation {
    width: auto;
    margin-left: calc((100% - 696px) / 8);

    @media (max-width: $viewport--md) {
      margin-left: calc((100% - 519px) / 8);
    }

    @media (max-width: $viewport--sm) {
      margin-left: 0;
    }
  }

  &__item-navigation--active {
    position: relative;

    @media (max-width: $viewport--sm) {
      &::before {
        content: "";
        width: 100%;
        position: absolute;
        bottom: -4px;
        left: 0;
        height: 2px;
        background-color: $color-venice-blue;
      }
    }
  }

  &__item-navigation a {
    display: block;
    font-family: $font-family--r;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: $color-default-white;
    transition: all 0.3s ease;

    &:hover {
      opacity: 0.7;
    }

    &:focus {
      outline: 1px solid $color-default-white;
    }

    @media (max-width: $viewport--md) {
      padding: 7px 2px;
      font-size: 14px;
      line-height: 16px;
    }

    @media (max-width: $viewport--sm) {
      font-size: 16px;
      line-height: 21px;
      color: $color-night-rider;
    }

    @media (max-width: $viewport--xs) {
      font-family: $font-family--r;
      font-weight: 500;
    }
  }

  &__control {
    display: none;
    position: absolute;
    bottom: -3px;
    align-items: center;
    height: 44px;
    z-index: 1;
    cursor: pointer;
    color: $color-air-force-blue;

    @media (max-width: $viewport--sm) {
      outline: $color-transparent;
      -webkit-tap-highlight-color: $color-transparent;
    }

    svg {
      transition: all 0.3s ease;
    }

    svg:hover {
      opacity: 0.7;
    }

    @media (max-width: $viewport--sm) {
      display: flex;
    }
  }

  &__control--button-prev {
    width: 10px;
    justify-content: flex-start;

    @media (max-width: $viewport--sm) {
      width: 50px;
      left: -45px;
      background: linear-gradient(90deg, rgba($color-default-white, 1), rgba($color-default-white, 0.9), rgba($color-default-white, 0.8), rgba($color-default-white, 0));
    }

    @media (max-width: $viewport--xs) {
      width: 35px;
      left: -25px;
      background: linear-gradient(90deg, rgba($color-default-white, 1), rgba($color-default-white, 0.9), rgba($color-default-white, 0.5), rgba($color-default-white, 0));
    }

    svg {
      transform: rotate(180deg);

      @media (max-width: $viewport--sm) {
        margin-left: 35px;
      }

      @media (max-width: $viewport--xs) {
        margin-left: 15px;
      }
    }
  }

  &__control--button-next {
    width: 70px;
    left: auto;
    justify-content: flex-end;

    @media (max-width: $viewport--sm) {
      width: 80px;
      right: -37px;
      background: linear-gradient(-90deg, rgba($color-default-white, 1), rgba($color-default-white, 0.9), rgba($color-default-white, 0.5), rgba($color-default-white, 0));

      svg {
        margin-right: 30px;
      }
    }

    @media (max-width: $viewport--xs) {
      width: 70px;
      right: -25px;

      svg {
        margin-right: 20px;
      }
    }
  }

  &__control.swiper-button-disabled {
    display: none;
  }
}
