.user-menu__list,
.user-menu__box {
  margin: 0;
  padding: 0;
  list-style: none;
}

.user-menu__line {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  &--indent {
    margin-right: 21px;

    @include viewport--xb {
      margin-right: 0;
    }
  }
}

.user-menu__img-logo {
  @include viewport--bg {
    margin-right: 30px;
  }
}

.user-menu__item {
  @include viewport--xb {
    margin-left: 12px;
  }

  &--city {
    max-width: 169px;
    width: 169px;

    svg {
      min-width: 23px;
    }
  }

  &--small-indent {
    margin-right: 22px;
  }

  &--smaller-indent {
    margin-right: 15px;

    @include viewport--xb {
      margin-right: 3px;
    }
  }

  &--tiny-indent {
    margin-right: 10px;
  }
}

.user-menu__link--city .user-menu__current-city {
  color: $color-default-white;
}

.user-menu__current-city {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-menu__link {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 5px;
  color: $color-default-white;
  outline: none;

  svg {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    fill: $color-default-white;
    color: $color-default-white;
    transition: color $default-transition-settings, fill $default-transition-settings, opacity $default-transition-settings;
  }

  &--line::before {
    position: absolute;
    top: 0;
    right: -12px;
    content: "";
    width: 2px;
    height: 24px;
    background-color: $color-default-white;
  }

  &--phone::after {
    position: absolute;
    top: -12px;
    left: -10px;
    content: "";
    width: 40px;
    height: 40px;
    background-color: transparent;
    transition: color $default-transition-settings, opacity $default-transition-settings;
  }

  &:hover,
  &:focus {
    color: $color-air-force-blue;
  }

  &:active {
    opacity: 0.8;
  }

  .user-menu__text {
    transition: color $default-transition-settings, opacity $default-transition-settings;
  }

  &:hover .user-menu__text,
  &:focus .user-menu__text {
    color: $color-air-force-blue;
  }

  &:active .user-menu__text {
    opacity: 0.8;
  }

  &:hover svg,
  &:focus svg {
    fill: $color-air-force-blue;
    color: $color-air-force-blue;
  }

  &:active svg {
    opacity: 0.8;
  }
}

.user-menu__text {
  color: $color-default-white;
  outline: none;
}

.user-menu__button {
  @include viewport--xb {
    display: none;
  }

  @include viewport--xs {
    display: none;
  }
}

.user-menu__button-hamburger {
  margin-bottom: 8px;
  margin-left: 12px;
}

.user-menu__none-mobile {
  @include viewport--xb {
    display: none;
  }
}

.user-menu__none-tablet {
  @include viewport--bg {
    display: none;
  }
}

.user-menu__open-mobile {
  display: none;

  @include viewport--xb {
    display: block;
  }
}

.user-menu__open-tablet {
  display: none;

  @include viewport--bg {
    display: block;
  }
}

.open {
  @include viewport--xs {
    display: block;
  }
}
