.social-light__list {
  margin: 0;
  padding: 0;
  width: 196px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  list-style: none;
}

.social-light__item {
  &:hover,
  &:focus {
    opacity: 0.5;
    transition: $default-transition-settings;
  }

  &:active {
    opacity: 0.3;
  }
}

.social-light__link {
  outline: none;
}
