.promo-filter {
  // display: flex;
  flex-wrap: wrap;
  color: $color-blue-whale;
  margin-right: -14px;
  letter-spacing: 1px;

  @include viewport--xs {
    margin-right: -13px;
  }
}

.promo-filter__row {
  width: 100%;
  display: flex;
}

.promo-filter__more {
  position: relative;
  padding: 11px;
  padding-left: 32px;
  margin-right: 3px;
  margin-bottom: 13px;
  background-color: transparent;
  border: none;
  color: $color-air-force-blue;
  letter-spacing: 0.3px;
  cursor: pointer;

  @include viewport--xs {
    order: 1;
    margin-right: auto;
    margin-left: auto;
  }

  &:focus {
    outline: none;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    fill: $color-air-force-blue;
  }

  .promo-filter__more-close {
    display: none;
  }
}

.promo-filter .hide {
  display: none;
}

.promo-filter--open {
  .promo-filter__more {
    padding-left: 25px;

    @include viewport--xs {
      // width: calc(50% - 13px);
    }
  }

  .promo-filter__more-close {
    display: block;
  }

  .promo-filter__more-open {
    display: none;
  }

  .hide {
    display: block;
  }

  .promo-filter__checkbox--hide {
    display: block;
  }
}

.promo-filter__btn-map {
  width: 162px;
  margin-bottom: 13px;
  margin-right: 14px;
  margin-left: auto;

  @include viewport--md {
    // margin-left: 20px;
    width: 212px;
  }

  @include viewport--sm {
    width: 120px;
  }

  @include viewport--xs {
    width: calc(50% - 13px);
    margin-right: 13px;
    margin-left: 0;
    font-size: 16px;
  }
}

.promo-filter__btn-submit {
  width: 305px;
  margin-bottom: 13px;
  margin-right: 13px;
  // padding: 11px;
  // font-size: 16px;
  // line-height: 21px;
  // letter-spacing: 1px;
  // margin-left: auto;

  @include viewport--sm {
    width: 295px;
  }

  @include viewport--xs {
    order: 1;
    width: 100%;
    font-size: 16px;
    // padding: 12px;
    // letter-spacing: 0;
  }
}

.promo-filter__btns {
  display: flex;
  margin-left: auto;
}
