.sorting-by-tag {
  align-items: flex-start;
  width: 100%;

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    padding: 0;
    margin: 0;
    margin-left: -10px;
    list-style-type: none;
  }

  &__item {
    display: flex;

    margin-left: 10px;
    //margin-right: 5px;
    margin-bottom: 10px;

    font-family: $font-family--r;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 0.023em;

    color: $color-dim-gray;
    /*
    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
    */
  }

  &__link {
    padding: 11px 22px;

    color: $color-dim-gray;
    background-color: $color-alice-blue;
    border-radius: 4px;

    &:hover,
    &:active {
      //background-color: #8ea5ba; // ,  
      background-color: $color-alice-blue;
      color: $color-white;
      transition: $default-transition-settings;
    }
  }
}
