.quick-links {
  display: flex;
  max-width: 1366px;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  padding: 24px 78px 47px;
  margin: 0 auto;
}

.quick-links a {
  transition: all 0.3s;
}

.quick-links a:focus,
.quick-links a:hover {
  color: $color-denim;
  cursor: pointer;
  transition: all 0.3s;
}

.quick-links__list {
  margin-right: 52px;
  margin-bottom: 10px;
}

.quick-links__list-block {
  max-width: 167px;
  list-style: none;
  padding: 0;
  margin: 0;
  color: $color-eclipse;
  font-family: $font-family--r;
  font-size: 13px;
  letter-spacing: 0.33px;
  line-height: 21.5px;
}

.quick-links__list-title {
  width: 94px;
  height: 23px;
  color: $color-charcoal;
  font-family: $font-family--r;
  font-size: 19px;
  font-weight: 500;
  letter-spacing: 0.47px;
  line-height: 34px;
}

.quick-links__list-link {
  padding-bottom: 5px;
}

.quick-links__list:last-of-type {
  margin-right: 0;
}

.quick-links__list:nth-child(1) .quick-links__list-item:nth-child(4),
.quick-links__list:nth-child(2) .quick-links__list-item:nth-child(4) {
  padding-top: 22px;
}

@media (max-width: $viewport--md) {
  .quick-links__list-item {
    max-width: 99px;
  }

  .quick-links {
    padding: 24px 60px 47px;
    justify-content: flex-start;
  }

  .quick-links__list-item:nth-child(5) {
    margin-right: 40px;
  }

  .quick-links__list:nth-child(5) {
    margin-right: 43px;
  }

  .quick-links__list:nth-child(6) {
    margin-right: 84px;
  }
}

@media (max-width: $viewport--sm) {
  .quick-links {
    flex-wrap: wrap-reverse;
    justify-content: space-between;
    margin-bottom: 0;
    padding: 36px 20px 6px;
  }

  .quick-links__list {
    width: 50%;
    margin-bottom: 20px;
    margin-right: 0;
  }

  .quick-links__list:nth-child(6) {
    margin-right: 0;
  }

  .quick-links__list:nth-child(3) {
    order: -1;
  }

  .quick-links__list:nth-child(4) {
    order: -1;
  }

  .quick-links__list:nth-child(5) {
    display: none;
  }
}
