.card-size {
  display: flex;

  height: 38px;

  border: 2px solid $color-air-force-blue;
  border-radius: 4px;

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 33px;

    &--tile {
      color: $color-air-force-blue;
    }

    &--list {
      //color: $color-white;
      color: $color-air-force-blue;
    }

    &--active {
      background-color: $color-air-force-blue;
      pointer-events: none;
      color: $color-white;
    }
  }
}
