.history-timeline-year {
  --clip-pathed-ribbon-right: polygon(87% 0, 100% 50%, 87% 100%, 0 100%, 0 0);
  --clip-pathed-ribbon-left: polygon(13% 0, 100% 0, 100% 100%, 13% 100%, 0 50%);

  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 93px;
  height: 40px;
  padding: 8px 20px 10px 10px;

  color: $color-white;
  font-family: $font-family--m;
  font-size: 25px;
  font-weight: 500;
  text-align: center;

  //background-color: #4d80a0;
  background-color: $color-air-force-blue;
  clip-path: var(--clip-pathed-ribbon-right);
}

@include viewport--xb {
  .history-timeline-year {
    position: relative;
    z-index: 1;

    margin-bottom: 15px;

    //color: #4d80a0;
    color: $color-air-force-blue;

    background-color: $color-white;

    &.history-timeline-year--filled {
      color: $color-white;

      //background-color: #4d80a0;
      background-color: $color-air-force-blue;

      &::before {
        //background-color: #4d80a0;
        background-color: $color-air-force-blue;
      }
    }

    &:not(:first-of-type) {
      margin-top: -14px;
    }

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;

      transform: translateX(-50%) translateY(-50%);

      content: "";
    }

    &::before {
      z-index: -1;

      width: calc(100% - 4px);
      height: calc(100% - 4px);

      background-color: $color-white;
    }

    &::after {
      z-index: -2;

      width: 100%;
      height: 100%;

      //background-color: #4d80a0;
      background-color: $color-air-force-blue;
    }

    &--zero-side {
      margin-right: 30px;

      transform: translateX(-50%);

      &,
      &::before,
      &::after {
        clip-path: var(--clip-pathed-ribbon-right);
      }
    }

    &--one-side {
      margin-left: 30px;
      padding: 8px 10px 10px 20px;

      transform: translateX(50%);

      &,
      &::before,
      &::after {
        clip-path: var(--clip-pathed-ribbon-left);
      }
    }
  }
}
