.banner-consultation {
  position: relative;

  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 65px;
  padding: 65px 45px;

  font-family: $font-family--m;
  font-size: 21px;
  line-height: 31px;
  letter-spacing: 0.033em;
  color: $color-night-rider;
  background-color: $color-pink-swan;

  border-radius: 6px;
  backdrop-filter: blur(29px);

  @include viewport--md {
    padding: 33px 27px;
    flex-direction: column;

    font-size: 15px;
    line-height: 22px;
  }

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    border-radius: 6px;

    background-image: linear-gradient(rgba($color-default-white, 0.6), rgba($color-default-white, 0.6));

    content: "";
  }

  &__bg-image {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    border-radius: 6px;

    object-fit: cover;
  }

  &__title {
    font-size: 40px;
    line-height: 52px;

    @include viewport--xs {
      margin-bottom: 14px;

      font-size: 27px;
      line-height: 32px;
    }
  }

  &__text {
    margin-top: 0;

    @include viewport--xs {
      margin-bottom: 21px;
    }
  }
}

.banner-consultation__text-content {
  display: flex;
  flex-direction: column;
  width: 46%;
  padding-right: 46px;

  @include viewport--md {
    width: 100%;
    padding-right: 0;
  }
}

.banner-consultation__form-wrapper {
  width: 54%;

  @include viewport--md {
    width: 100%;
  }
}

.banner-consultation__form {
  display: flex;
  flex-direction: column;

  flex-wrap: wrap;
  width: 100%;
}

.banner-consultation__wrapper {
  display: flex;
  width: 100%;
  align-items: flex-start;
  font-family: $font-family--r;

  @include viewport--xs {
    max-width: 314px;
    flex-direction: column;
  }

  label {
    width: calc(50% - 13px);
    margin-bottom: 27px;
    margin-right: 26px;

    @include viewport--md {
      width: calc(50% - 13px);
    }

    @include viewport--xs {
      width: 100%;
      margin-right: 0;
      margin-bottom: 13px;
    }

    &:last-of-type {
      margin-right: 0;
    }

    input {
      color: $color-night-rider;
      background: transparent;
      border: 2px solid $color-gray-2;

      @include viewport--sm {
        padding-left: 24px;

        font-size: 14px;
      }

      &:hover,
      &:focus {
        background-color: $color-white;
        border-color: $color-white;
      }
    }

    input::placeholder {
      color: $color-night-rider;
      opacity: 1;
    }
  }
}

.banner-consultation__consent {
  width: calc(100% - 194px);
  margin: 5px 0 0 0;
  padding-right: 20px;
  text-align: left;
  font-size: 15px;
  line-height: 19px;
  font-weight: normal;
  letter-spacing: 0.023em;

  @include viewport--xs {
    width: 100%;
    padding-right: 0;

    font-size: 9px;
    line-height: 11px;

    order: 2;
  }
}

.banner-consultation__consent.agreement-text a {
  @include viewport--xs {
    color: $color-night-rider;
  }
}

.banner-consultation__button-submit {
  width: 194px;
  height: 43px;

  @include viewport--xs {
    width: 100%;
    margin-bottom: 10px;
    padding-right: 0;

    order: 1;
  }
}
