.advertisement__box {
  position: relative;

  padding: 0 46px;
  padding-top: 52px;
  padding-bottom: 38px;
  margin-bottom: 78px;

  width: 100%;
  min-height: 258px;

  font-family: $font-family--m;
  color: $color-default-white;
  letter-spacing: 0.25px;

  display: flex;
  flex-direction: column;

  @media (max-width: $viewport--xs) {
    margin-bottom: 53px;
    padding: 0 18px;
    padding-top: 47px;
    padding-bottom: 26px;
    min-height: 418px;
  }

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    border-radius: 6px;

    background-image: linear-gradient(rgba(8, 44, 75, 0.38), rgba(8, 44, 75, 0.38));

    content: "";
  }

  .advertisement__box-bg-img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    border-radius: 6px;

    object-fit: cover;
  }
}

.advertisement__title {
  display: block;
  margin: 0;
  padding: 0;
  margin-bottom: 17px;

  font-size: 29px;
  line-height: 32px;
  font-weight: 600;

  @include viewport--xs {
    max-width: 276px;
    margin-bottom: 30px;
  }
}

.advertisement__text-content {
  max-width: 582px;
  width: 100%;
  margin-bottom: 25px;
}

.advertisement__text {
  margin: 0;
  padding: 0;

  font-size: 18px;
  line-height: 26px;
}

.advertisement__link {
  margin-top: auto;
  width: 216px;
  height: 44px;

  @include viewport--xs {
    width: 201px;

    background-color: transparent;
    color: $color-default-white;
    border: 3px solid $color-default-white;
  }
}
