.mission {
  padding-bottom: 128px;

  @media (max-width: $viewport--md) {
    padding-bottom: 35px;
  }

  &__wrap {
    display: flex;

    @media (max-width: $viewport--md) {
      display: block;
    }

    @media (max-width: $viewport--xs) {
      padding-left: 5px;
      padding-right: 5px;
    }
  }

  &__features {
    width: 41%;

    @media (max-width: $viewport--md) {
      width: 100%;
    }

    .h2 {
      margin-bottom: 46px;

      @media (max-width: $viewport--md) {
        margin-bottom: 30px;
      }

      @media (max-width: $viewport--xs) {
        margin-bottom: 13px;
      }
    }
  }

  &__list-features {
    list-style: none;
    margin: 0;
    padding-left: 50px;
    padding-right: 30px;

    @media (max-width: $viewport--md) {
      display: flex;
      margin-left: -20px;
      margin-bottom: 30px;
      padding-left: 0;
      padding-right: 0;
    }

    @media (max-width: $viewport--sm) {
      display: flex;
      flex-direction: column;
      margin-left: 0;
    }

    @media (max-width: $viewport--xs) {
      margin-bottom: 0;
    }
  }

  &__item-features {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;

    @media (max-width: $viewport--md) {
      display: flex;
      justify-content: flex-start;
      width: calc(33% - 20px);
      margin-left: 20px;
    }

    @media (max-width: $viewport--sm) {
      max-width: 440px;
      width: 100%;
      margin-left: 0;
    }

    @media (max-width: $viewport--xs) {
      flex-direction: column;
      max-width: none;
      margin-bottom: 15px;
    }
  }

  &__item-features svg {
    width: 19%;
    margin-top: 10px;

    @media (max-width: $viewport--md) {
      width: 23%;
      margin-top: 30px;
    }

    @media (max-width: $viewport--xs) {
      width: 69px;
      height: 69px;
      margin-top: 25px;
      margin-left: 5px;
      margin-bottom: 4px;
    }
  }

  &__item__description {
    width: 73%;
    margin-left: 8%;

    @media (max-width: $viewport--md) {
      width: 73%;
      margin-left: 4%;
    }

    @media (max-width: $viewport--xs) {
      margin-left: 0;
      width: 100%;
    }

    h3 {
      margin: 0;
      margin-bottom: 8px;
      font-family: $font-family--m;
      font-size: 21px;
      line-height: 38px;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: $color-air-force-blue;

      @media (max-width: $viewport--md) {
        font-size: 18px;
        line-height: 30px;
      }

      @media (max-width: $viewport--xs) {
        font-size: 21px;
        line-height: 36px;
        margin-bottom: 6px;
      }
    }

    p {
      margin: 0;
      font-family: $font-family--r;
      font-size: 16px;
      line-height: 19px;
      font-weight: 400;
      letter-spacing: 0.025em;
      color: $color-night-rider;
    }
  }

  &__block-slider {
    width: 59%;

    @media (max-width: $viewport--md) {
      width: 100%;
    }
  }

  &__slider.swiper-container {
    position: relative;
    overflow: hidden;
  }

  &__list-slides.swiper-wrapper {
    list-style: none;
    display: flex;
    flex-wrap: nowrap;
    margin: 0;
    padding-left: 0;
  }

  &__slide.swiper-slide {
    width: 100%;
    flex-shrink: 0;
    border-radius: 6px;

    @media (max-width: $viewport--xs) {
      overflow: hidden;
    }
  }

  &__slide img {
    @media (max-width: $viewport--md) {
      width: 902px;
      height: auto;
      object-fit: cover;
    }

    @media (max-width: $viewport--xs) {
      height: 284px;
      width: auto;
      object-fit: cover;
      object-position: 60% 50%;
    }
  }

  &__pagination.swiper-pagination-bullets {
    position: absolute;
    display: block;
    left: 0;
    bottom: 25px;
    width: 100%;
    height: 23px;
    text-align: center;

    @media (max-width: $viewport--xs) {
      bottom: 13px;
      height: 12px;
    }
  }

  &__pagination .swiper-pagination-bullet {
    display: inline-block;
    vertical-align: middle;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    border: 0;
    opacity: 1;
    background-color: $color-default-white;
    transition: all 0.3s ease;

    &:hover:not(.swiper-pagination-bullet-active),
    &:focus:not(.swiper-pagination-bullet-active) {
      opacity: 0.7;
    }

    @media (max-width: $viewport--xs) {
      width: 6px;
      height: 6px;
    }
  }

  &__pagination .swiper-pagination-bullet + .swiper-pagination-bullet {
    margin-left: 11px;

    @media (max-width: $viewport--xs) {
      margin-left: 8px;
    }
  }

  &__pagination .swiper-pagination-bullet-active {
    width: 23px;
    height: 23px;
    border: 3px solid $color-default-white;
    background-color: $color-denim;

    @media (max-width: $viewport--xs) {
      width: 12px;
      height: 12px;
    }
  }

  &__control.swiper-button-prev,
  &__control.swiper-button-next {
    margin-top: 0;
    width: 22px;
    height: 40px;
    position: absolute;
    fill: $color-default-white;
    transition: all 0.3s ease;

    &:hover {
      opacity: 0.7;
    }

    @media (max-width: $viewport--sm) {
      outline: $color-transparent;
      -webkit-tap-highlight-color: $color-transparent;
    }
  }

  &__control.swiper-button-prev svg,
  &__control.swiper-button-next svg {
    width: 23px;
    height: 41px;

    @media (max-width: $viewport--xs) {
      width: 18px;
      height: 33px;
    }
  }

  &__control.swiper-button-prev::after,
  &__control.swiper-button-next::after {
    display: none;
  }

  &__control.swiper-button-prev {
    left: 4.9%;
    top: 50%;
    transform: translateY(-50%);

    @media (max-width: $viewport--xs) {
      left: 10px;
    }
  }

  &__control.swiper-button-next {
    right: 4.9%;
    top: 50%;
    transform: translateY(-50%);

    @media (max-width: $viewport--xs) {
      right: 10px;
    }
  }

  &__control.swiper-button-disabled {
    display: none;
  }
}
